<template>
  <div class="takeOut animated fadeIn">
    <div class="head">
      <span class="span1">
        <router-link to="/searchPro">
          <i class="fa fa-search fa-2x"></i>
        </router-link>
      </span>
      <div class="nav">
        <router-link to="/">{{locateName}}</router-link>
      </div>
      <span class="span2">
        <router-link to="/personal">
          <i class="fa fa-user-o fa-2x"></i>
        </router-link>
      </span>
    </div>
    <div class="swiperImg">
      <div class="swiper">
        <swiper :options="swiperOption" v-if="flag">
          <!-- slides -->
          <swiper-slide>
            <ul class="swiperUrl">
              <li v-for="(list,index) in eightlists" :key="index" @click=goSelect(list.title,list.id)>
                <img :src="$store.state.baseUrl+list.image_url" alt>
                <br>
                <span>{{list.title}}</span>
              </li>
            </ul>
          </swiper-slide>
          <swiper-slide>
            <ul class="swiperUrl">
              <li v-for="(list,index) in sixLists " :key="index" @click=goSelect(list.title,list.id)>
                <img :src="$store.state.baseUrl+list.image_url" alt>
                <br>
                <span>{{list.title}}</span>
              </li>
            </ul>
          </swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination" slot="pagination"></div>
          <!-- <div class="swiper-scrollbar" slot="scrollbar"></div> -->
        </swiper>
        <div class="line"></div>
      </div>
    </div>
    <div class="shopList">
      <div class="shoper">
        <i class="fa fa-home"></i>
        <span class="nearBy">附近商家</span>
      </div>
      <div class="shopShow" v-if="shops!=''">
        <ul>
          <li v-for="(shop,index) in shops" :key="index" @click="goDetail(shop)">
            <div class="shopLeft">
              <img :src="$store.state.shopBaseUrl + shop.image_path" alt>
            </div>
            <div class="shopRight">
              <div class="shopHead">
                <span class="brand">品牌</span>
                <span class="shopName">{{shop.name}}</span>
                <div class="promise">
                  <span v-for="(word,index) in shop.supports" :key="index">{{word.icon_name}}</span>
                </div>
              </div>
              <div class="shopRate">
                <el-rate
                  v-model="shop.rating"
                  disabled
                  show-score
                  text-color="#ff9900"
                  score-template="{value}"
                ></el-rate>
                <span class="sale">月售{{shop.recent_order_num}}单</span>
                <div class="promise pro2">
                  <span class="pText">{{shop.delivery_mode.text}}}</span>
                  <span class="pText2">准时到达</span>
                </div>
              </div>
              <div class="shopDistance">
                <p
                  class="money"
                >￥{{shop.float_minimum_order_amount}}起送/{{shop.piecewise_agent_fee.tips}}</p>
                <p class="time">
                  {{shop.distance}}/
                  <span class="timeCol">{{shop.order_lead_time}}</span>
                </p>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "takeOut",
  data() {
    return {
      lists: "",
      flag: false,
      shops:"",
      locateName:"",
      eightlists: [],
      sixLists: [],
      swiperOption: {
        loop: true,
        pagination: {
          el: ".swiper-pagination"
        }
      }
    };
  },
  created() {
    this.allLocate();
    this.getLists();
    this.getShops();
  },
  methods: {
    allLocate() {
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/v2/pois/" +
           localStorage.getItem('latitude') +
          "," +
           localStorage.getItem('longitude')
      })
        .then(result => {
            this.locateName = result.data.name;
        })
        .catch(err => {});
    },
    // 获取所有商铺分类列表
    getLists() {
      this.$http({
        method: "get",
        url: "https://elm.cangdu.org/v2/index_entry"
      })
        .then(result => {
          console.log(this.$store.state.foodNum);
          this.lists = result.data;
          this.flag = true;
          this.eightlists = this.lists.splice(0, 8);
          this.sixLists = this.lists.splice(0, 8);
        })
        .catch(err => {});
    },
    getShops() {
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/shopping/restaurants?latitude=" +
          localStorage.getItem('latitude') +
          "&longitude=" +
             localStorage.getItem('longitude')
      })
        .then(result => {
          // console.log(result.data)
           this.shops = result.data;
        })
        .catch(err => {});
    },
    goDetail(shop) {
      // console.log(shop);
     this.$store.state.shopInfo = shop;
     console.log()
      this.$router.push({ name: "storeDetail"});
    },
    goSelect(titleYa,idYa){
      // console.log(titleYa,idYa)
      this.$router.push({name:'classify',params:{title:titleYa,id:idYa}})
    }
  }
};
</script>
<style scoped>
.animated {
  animation-duration: 1s;
}
a {
  color: white;
}
.head {
  /* width: 100%; */
  height: 0.457rem;
  background: #3190e8;
  line-height: 0.457rem;
  text-align: center;
  color: white;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
  padding-left: 0.1875rem;
  padding-right: 0.1875rem;
}
.nav {
  width: 1.875rem;
  margin: 0 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}
.nav a {
  font-size: 0.18rem;
  color: white;
  font-weight: bold;
}
.span1 {
  float: left;
  padding-top: 0.045rem;
}
.span1 i {
  font-size: 0.21rem;
}
.span2 {
  float: right;
  padding-top: 0.045rem;
}
.span2 i {
  font-size: 0.21rem;
}
.swiperImg {
  background: white;
  padding-top: 0.54337rem;
  border-bottom: 0.00641rem solid #e4e4e4;
  /* width: 100%; */
  height: 1.984rem;
}
.swiper {
  background: white;
  /* width: 100%; */
  height: 1.7256rem;
}
.swiperUrl li {
  float: left;
  width: 25%;
  height: 0.8628rem;
  text-align: center;
  font-size: 0.12rem;
  color: #666;
}
.swiperUrl li img {
  width: 0.4219rem;
  margin-bottom: 0.07rem;
}
.line {
  height: 0.14rem;
  /* width: 100%; */
  background: #f5f5f5;
  margin-top: 0.14rem;
  border-top: 0.01rem solid #e4e4e4;
  border-bottom: 0.01rem solid #e4e4e4;
}
.shopList {
  background: #fff;
  padding-left: 0.14rem;
      padding-bottom: 0.46rem;
}
.shopList li {
  overflow: hidden;
  /* display: flex; */
  border-bottom: 0.005rem solid #f1f1f1;
  padding: 0.16rem 0;
}
.shoper {
  line-height: 0.38rem;
  padding-left: 0.14rem;
  color: #999;
  font-size: 0.14rem;
}
.shopLeft {
  float: left;
  width: 0.72rem;
  height: 0.72rem;
  /* background: red; */
}
.shopLeft img {
  width: 0.6328rem;
}
.shopRight {
  float: right;
  padding-left: 0.05rem;
  width: 2.836rem;
  height: 0.716rem;
}
.shopHead {
  height: 0.1715rem;
  /* background: red; */
}
.shopRate,
.shopDistance,
.shopHead {
  padding-right: 0.14rem;
  width: 2.7rem;
}
.shopRate {
  height: 0.14rem;

  margin-top: 0.12rem;
}
.shopDistance {
  height: 0.16rem;

  margin-top: 0.12rem;
}
.brand {
  width: 0.24rem;
  height: 0.14rem;
  padding: 0, 0.02344rem;
  margin-right: 0.04688rem;
  background: #ffd930;
  font-size: 0.016rem;
  font-weight: bold;
}
.shopName {
  font-weight: bold;
  font-size: 0.15rem;
}
.el-rate {
  transform: scale(0.5);
  margin-left: -0.9rem;
}
.promise {
  float: right;
  font-size: 0.12rem;
  color: #999;
}
.promise span {
  margin-right: 0.005rem;
  border: 0.005rem solid #f1f1f1;
}
.pText {
  background: #57a9ff;
  font-size: 0.01rem;
}
.pText2 {
  font-size: 0.1rem;
}
.pro2 {
  margin-top: -0.2rem;
}
.money {
  transform: scale(0.9);
  float: left;
  font-size: 0.12rem;
  color: #666;
}
.time {
  transform: scale(0.9);
  font-size: 0.12rem;
  color: #666;
  float: right;
}
.timeCol {
  color: #57a9ff;
}
.sale {
  transform: scale(0.2);
  font-size: 0.04rem;
  position: relative;
  left: 0.75rem;
  top: -0.2rem;
}
.el-rate__icon {
  margin-right: 0;
}
</style>



