import Vue from "vue";
import Router from "vue-router";
import home from "@/components/home/home";
import login from "@/components/login/login";
import order from "@/components/order/order";
import search from "@/components/search/search";
import resetCode from "@/components/login/resetCode";
import orderChoice from "@/components/order/homeChoice/orderChoice";
import takeOut from "@/components/order/homeChoice/takeOut";
import searchPro from "@/components/order/homeChoice/searchPro";
import personal from "@/components/order/homeChoice/personal";
import storeDetail from "@/components/order/homeChoice/storeDetail/storeDetail";
// 商品 
import product from "@/components/order/homeChoice/storeDetail/detailWays/product";
// 评价
import evalute from "@/components/order/homeChoice/storeDetail/detailWays/evalute";
// 店铺信息
import shopInfor from "@/components/order/homeChoice/storeDetail/shopInfor";
// 确认订单
import confirmOrder from "@/components/order/homeChoice/storeDetail/detailWays/confirmOrder";
//添加地址
import getOrder from "@/components/order/homeChoice/storeDetail/detailWays/getOrder";
// 地址添加
import makeAdd from "@/components/order/homeChoice/storeDetail/detailWays/makeAdd";
//发票台头
import invoiceTitle from "@/components/order/homeChoice/storeDetail/detailWays/invoiceTitle";
//口味选择
import chooseTaste from "@/components/order/homeChoice/storeDetail/detailWays/chooseTaste";
// 商品分类
import classify from "@/components/order/homeChoice/classify";
Vue.use(Router);


import Account from '@/components/order/homeChoice/perBran/account'
import balance from '@/components/order/homeChoice/perBran/balance'
import balanceIssue from '@/components/order/homeChoice/perBran/balance/balanceIssue'
import discounts from '@/components/order/homeChoice/perBran/discounts'
import redPacket from '@/components/order/homeChoice/perBran/discounts/redPacket'
import redexplain from '@/components/order/homeChoice/perBran/discounts/redexplain'
import historyred from '@/components/order/homeChoice/perBran/discounts/historyred'
import exchange from '@/components/order/homeChoice/perBran/discounts/exchange'
import recommend from '@/components/order/homeChoice/perBran/discounts/recommend'
import voucher from '@/components/order/homeChoice/perBran/discounts/voucher'
import saidvoucher from '@/components/order/homeChoice/perBran/discounts/saidvoucher'
import download from '@/components/order/homeChoice/perBran/download'
import integral from '@/components/order/homeChoice/perBran/integral'
import integralissue from '@/components/order/homeChoice/perBran/integral/integralissue'
import malfunction from '@/components/order/homeChoice/perBran/malfunction'
import elmvip from '@/components/order/homeChoice/perBran/elmvip'
import privilege from '@/components/order/homeChoice/perBran/elmvip/privilege'
import payment from '@/components/order/homeChoice/perBran/elmvip/payment'
import vipExchange from '@/components/order/homeChoice/perBran/elmvip/vipExchange'
import purchase from '@/components/order/homeChoice/perBran/elmvip/purchase'
import serve from '@/components/order/homeChoice/perBran/serve'
import rename from '@/components/order/homeChoice/perBran/account/rename'
import addressed from '@/components/order/homeChoice/perBran/account/addressed'
import increase from '@/components/order/homeChoice/perBran/account/increase'
import resetpassword from '@/components/order/homeChoice/perBran/account/resetpassword'
import searchAddress from '@/components/order/homeChoice/perBran/account/searchAddress'




export default new Router({
  routes: [
    { path: "/", name: "home", component: home },
    { path: "/login", name: "login", component: login },
    {
      path: "/order",
      name: "order",
      component: order,
      redirect: "/searchPro",
      children: [
        { path: "/orderChoice", name: "orderChoice", component: orderChoice },
        { path: "/searchPro", name: "searchPro", component: searchPro },
        { path: "/takeOut", name: "takeOut", component: takeOut },
        { path: "/personal", name: "personal", component: personal },
        {path:'/account',name:'account',component:Account},
      ]
    },
    { path: "/search", name: "search", component: search },
    { path: "/resetCode", name: "resetCode", component: resetCode },
    {path:'/balance',name:'balance',component:balance},
    {path:'/balanceIssue',name:'balanceIssue',component:balanceIssue},
    {path:'/discounts',name:'discounts',component:discounts,redirect:'/redPacket',
    children:[
      {path:'/redPacket',name:'redPacket',component:redPacket},
      {path:'/voucher',name:'voucher',component:voucher},
    ]},
    {
      path: "/storeDetail",
      name: "storeDetail",
      component: storeDetail,
      redirect: { name: "product" },
      children: [
        { path: "/product", name: "product", component: product },
        { path: "/evalute", name: "evalute", component: evalute }
      ]
    },
    { path: "/shopInfor", name: "shopInfor", component: shopInfor },
    {path:'/confirmOrder',name:'confirmOrder',component:confirmOrder},
    {path:'/getOrder',name:'getOrder',component:getOrder},
    {path:'/makeAdd',name:'makeAdd',component:makeAdd},
    {path:'/classify',name:'classify',component:classify},
    {path:'/invoiceTitle',name:'invoiceTitle',component:invoiceTitle},
    {path:'/chooseTaste',name:'chooseTaste',component:chooseTaste},
    {path:'/redexplain',name:'redexplain',component:redexplain},
    {path:'/historyred',name:'historyred',component:historyred},
    {path:'/exchange',name:'exchange',component:exchange},
    {path:'/recommend',name:'recommend',component:recommend},
    {path:'/saidvoucher',name:'saidvoucher',component:saidvoucher},
    {path:'/download',name:'download',component:download},
    {path:'/integral',name:'integral',component:integral},
    {path:'/integralissue',name:'integralissue',component:integralissue},
    {path:'/malfunction',name:'malfunction',component:malfunction},
    {path:'/elmvip',name:'elmvip',component:elmvip},
    {path:'/privilege',name:'privilege',component:privilege},
    {path:'/payment',name:'payment',component:payment},
    {path:'/vipExchange',name:'vipExchange',component:vipExchange},
    {path:'/purchase',name:'purchase',component:purchase},
    {path:'/serve',name:'serve',component:serve},
    {path:'/rename',name:'rename',component:rename},
    {path:'/addressed',name:'addressed',component:addressed},
    {path:'/increase',name:'increase',component:increase},
    {path:'/resetpassword',name:'resetpassword',component:resetpassword},
    {path:'/searchAddress',name:'searchAddress',component:searchAddress},
  ]
});



