<template>
  <div class="chooseTaste">
    <div class="choose">
      <div class="chooseDiv" @click="titled()">
        <i class="fa fa-angle-left fa-2x"></i>
      </div>
      <span class="chSpan">口味选择</span>
    </div>
    <div class="radio">
      <p class="p1">快速备注</p>
      <div class="radioDiv1">
        <el-radio-group v-model="radios1">
          <el-radio-button label="不要辣">不要辣</el-radio-button>
          <el-radio-button label="少点辣">少点辣</el-radio-button>
          <el-radio-button label="多点辣">多点辣</el-radio-button>
        </el-radio-group>
      </div>
      <div class="radioDiv2">
        <el-radio-group v-model="radios2">
          <el-radio-button label="不要香菜">不要香菜</el-radio-button>
        </el-radio-group>
      </div>
      <div class="radioDiv3">
        <el-radio-group v-model="radios3">
          <el-radio-button label="不要洋葱">不要洋葱</el-radio-button>
        </el-radio-group>
      </div>
      <div class="radioDiv4">
        <el-radio-group v-model="radios4">
          <el-radio-button label="多点醋">多点醋</el-radio-button>
        </el-radio-group>
      </div>
      <div class="radioDiv5">
        <el-radio-group v-model="radios5">
          <el-radio-button label="多点葱">多点葱</el-radio-button>
        </el-radio-group>
      </div>
      <div class="radioDiv6">
        <el-radio-group v-model="radios6">
          <el-radio-button label="去冰">去冰</el-radio-button>
          <el-radio-button label="少冰">少冰</el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="textDiv">
        <p class="p2">其他备注</p>
        <textarea placeholder="请输入备注内容(可不填)" class="input_text" v-model="text">
        </textarea>
    </div>
    <div class="butDiv" @click="sss()">
        确定
    </div>
  </div>
</template>
<script>
export default {
  name: "chooseTaste",
  data() {
    return {
      radios1: "",
      radios2: "",
      radios3: "",
      radios4: "",
      radios5: "",
      radios6: "",
      text:"",
    };
  },
 
  methods: {
      sss(){
          this.$router.push({
              name:"confirmOrder",
              params:{
                  radios1: this.radios1 + this.radios2 + this.radios3 + this.radios4+ this.radios5 + this.radios6 + this.text
                //   radios2:this.radios2,
                //   radios3:this.radios3,
                //   radios4:this.radios4,
                //   radios5:this.radios5,
                //   radios6:this.radios6,
                //   text:this.text,
                }
          })
      },
    titled() {
      this.$router.go(-1);
    }
  }
};
</script>
<style scoped>
.chooseTaste{
    width: 100%;
    height: 100%;
     background-color: #f9f9f9;
    overflow: hidden;
}
.choose {
  width: 100%;
  height: 0.457rem;
  background: #3190e8;
  line-height: 0.457rem;
  text-align: center;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
.chooseDiv {
  float: left;
  padding-left: 0.14rem;
}
.chSpan {
  font-size: 0.2rem;
  font-weight: bold;
  margin-left: -0.3rem;
}
.radio {
  overflow: hidden;
  background-color: #fff;
  height: 2.5rem;
  margin-top: 0.6rem;
}
.radioDiv1 {
  margin-left: 0.1rem;
  margin-top: .5rem;
}
.radioDiv2 {
  position: relative;
  right: -2.7rem;
  top: -1.4rem;
  margin-top: 1rem;
}
.radioDiv3 {
  position: relative;
  left: 0.1rem;
  top: -2.3rem;
  margin-top: 1rem;
}
.radioDiv4 {
  position: relative;
  left: 1.2rem;
  top: -3.7rem;
  margin-top: 1rem;
}
 .radioDiv5 {
  position: relative;
  left: 2.2rem;
  top: -5.1rem;
  margin-top: 1rem;
}
.radioDiv6 {
  position: relative;
  left: 0.1rem;
  top: -6rem;
  margin-top: 1rem;
} 
.p1{
    position: absolute;
    top: 0.8rem;
    left: 0.1rem;
}
.textDiv{
    width: 100%;
    height: 2rem;
    padding-bottom: 0.1rem;
    overflow: hidden;
    margin-top: 0.2rem;
    background-color: #fff;
}
.p2{
    position: absolute;
    top: 3.5rem;
    left: 0.1rem;
}
.input_text{
    width: 90%;
    height: 1rem;
    background-color: #f9f9f9;
    border: .01rem solid #eee;
    resize: none;
    border-radius: .05rem;
    font-size: .1rem;
    color: #666;
    padding: .02rem;
    margin-left: 0.14rem;
    margin-top: 0.5rem;
}
.butDiv{
    background-color: #4cd964;
    font-size: .1rem;
    color: #fff;
    text-align: center;
    margin: 0 .5rem;
    line-height: .4rem;
    border-radius: .05rem;
}

</style>


