<template>
  <div class="rename animated fadeInRight">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>修改用户名</div>
    </header>

    <section class="rename_con">
      <section class="rename_top">
        <input type="text" :class="{top_input: istopinput}" placeholder="输入用户名" v-model="num">
        <p v-if="isP">用户名只能修改一次（5-24字符之间）</p>
        <p v-else :class="{top_p: istopinput}">用户名长度在5到24位之间</p>
      </section>

      <section class="reset">
        <button
          @click="affirm()"
          :class="num.length>=5!=''&&'fontopacity'"
        >确认修改</button>
      </section>
    </section>
  </div>
</template>

<script>
import warn from "../../../../public/warn";

export default {
  name: "rename",
  data() {
    return {
      num: "",
      isP: true,
      istopinput: false
    };
  },
  created() {},
  components: {
    warn
  },
  watch: {
    num(){
      if (this.num.length>=5&&this.num.length<24) {
        this.isP = true;
        this.istopinput = false
      }else {
        this.isP = false
        this.istopinput = true
      }
    }
  },
  methods: {
    back1() {
      this.$router.go(-1);
    },
    affirm() {
      this.$store.state.userInfo.username = this.num;
      if (this.num == '') {
        this.isP = false
        this.istopinput = true
      }else if (this.num.length>=5) {
        this.$router.push({name:"account"})
      }
    }
  }
};
</script>


<style scoped>
.rename {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}
.animated {
  animation-duration: 0.3s;
}

/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

.rename_con {
  margin-top: 0.457rem;
  padding: 0 0.1rem;
}
.rename_top {
  padding-top: 0.09375rem;
}
.rename_top input {
  background: none;
  width: 3.48rem;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0.046875rem 0.0234375rem;
  line-height: 0.28125rem;
  font-size: 0.164rem;
  display: block;
}
.rename_top .top_input {
  border-color: #ea3106;
}
.rename_top .top_p {
  color: #ea3106;
}

.rename_top p {
  font-size: 0.1359375rem;
  color: #666;
  padding-top: 0.0234375rem;
  padding: 0.09375rem 0 0.234375rem;
}

.reset {
  width: 3.5rem;
  height: 0.46rem;
  background: #3199e8;
}
.reset button {
  border: 0 none; /*去掉边框*/
  width: 3.5rem;
  display: block;
  background: none;
  line-height: 0.46rem;
  font-size: 0.164rem;
  color: #fff;
  opacity: 0.6;
  transition: all 1s;
}

.reset .fontopacity {
  opacity: 1;
  transition: all 1s;
}
</style>



