<template>
  <div class="mal" @click="Btn()">
    <div class="malfunction">
      <h3>请重新进入</h3>
      <p>请返回后重新进入积分商城~</p>
      <img :src="img" alt>
    </div>
  </div>
</template>

<script>
import img from "../../../../assets/404.png";

export default {
  name: "malfunction",
  data() {
    return {
      img
    };
  },
  methods: {
    Btn() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.mal {
  width: 100%;
  height: 6.67rem;
}
.malfunction {
  text-align: center;
  /* overflow: hidden; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
h3 {
  font-size: 0.26rem;
  color: #0d7fd5;
  margin-bottom: 0.15rem;
}
p {
  font-size: 0.14rem;
  color: #666;
  margin-bottom: 0.19rem;
}
img {
  width: 1.76rem;
  height: 1.76rem;
  vertical-align: middle;
}
</style>



