<template>
  <div class="seletAdd">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>确认订单</div>
    </header>
    <div class="addMid">
      <ul>
        <li
         v-for="(list,index) in lists" :key="index"
          @click="goConfir(list)"
        >
          <span class="addSpan">✔</span>
          <div class="info">
            <p class="infoP">
              <span class="infoSpan1">{{list.name}}</span>
              <span class="infoSpan2">{{list.sex}}</span>
              <span class="infoSpan3">{{list.phone}}</span>
            </p>
            <p class="inp">
              <span class="inSpan">{{list.tag_type}}</span>
              {{list.address+list.address_detail}}
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div class="newAdd" @click="goNewAdd()">
      <i class="el-icon-circle-plus-outline"></i>
      新增收获地址
    </div>

  </div>
</template>
<script>
import headZj from "../../../../public/header/headZj";
export default {
  name: "getOrder",
  data() {
    return {
      lists:[]
    };
  },
  created() {
    this.$store.state.wanted = "选择地址";
    // console.log(this.$store.state.shopInfo)
    this.gain()
  },
  methods: {
    goNewAdd() {
      this.$router.push({ name: "makeAdd" });
    },
    goConfir(list) {
      this.$router.push({ name: "confirmOrder", params: { addInfo: list } });
    },
    gain(){
          const url = "https://elm.cangdu.org/v1/users/"+ this.$store.state.userInfo.user_id +"/addresses"
          this.$http({
            method:"get",
            url:url
          }).then((res) => {
            console.log(res.data)
            this.lists = res.data
            this.lists.reverse()
          })
        },
    back1(){
      this.$router.push({name:"confirmOrder"})
    }
  }
};
</script>
<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}
.addMid {
  overflow: hidden;
  padding-top: 0.5rem;
  padding-left: 0.14rem;
}
.addMid ul li {
  border-bottom: 0.01rem solid #e4e4e4;
  line-height: 0.3rem;
}
.addSpan{
    display: inline-block;
    width: 0.25rem;
    height: 0.25rem;
    background-color:yellowgreen;
    text-align: center;
    line-height: 0.25rem;
    color: white;
    border-radius: 50%;
    position: relative;
    top: 0.15rem;
    left: 0;
}
.info {
  width: 100%;
  display: inline-block;
  margin-left: 0.3rem;
  margin-top: -0.7rem;
}
.infoP{
    width: 100%;
}
.infoSpan1{
    font-size: 0.18rem;
    font-weight:700;
}
.inp{
    width: 100%;
    color:gainsboro;
    margin-top: -0.1rem;
    font-size: 0.15rem;
}
.inSpan{
    height: 0.15rem;
    display: inline-block;
    line-height: 0.15rem;
    font-size: 0.01rem;
    text-align: center;
    background-color: yellowgreen;
    padding: 0 0.03rem;
    color: white;
    border-radius: 0.03rem;
}
.newAdd {
  width: 100%;
  text-align: center;
  color: blue;
  position: fixed;
  bottom: 0;
  line-height: 0.31rem;
  padding: 0.117rem 0;
}
.el-icon-circle-plus-outline{
  font-size: 0.2rem; 
  color: blue;
}
</style>



