<template>
  <div class="redPacket">
    <!-- 红包列表 -->
    <section class="hongbao_container">
      <div class="hongbao_title">
        <section class="hongbao_num">
          有
          <span>3</span>个红包即将到期
        </section>
        <section class="hongbao_explain">
          <!-- 红包说明 -->
          <router-link to="/redexplain">
            <i class="el-icon-question"></i>
            <span>红包说明</span>
          </router-link>
        </section>
      </div>

      <!-- 列表 -->
      <ul class="hongbao_list">
        <li class="hongbao_list_li" v-for="(list,index) in $store.state.hotbagList" :key="index">
          <section class="list_item">
            <!-- 可用红包列表左 -->
            <div class="list_item_left">
              <span>￥</span>
              <span>{{list.amount}}</span>
              <p>{{list.description_map.sum_condition}}</p>
            </div>
            <!-- 可用红包列表中 -->
            <div class="list_item_con">
              <h4>{{list.name}}</h4>
              <p>{{list.description_map.validity_periods}}</p>
              <p>{{list.description_map.phone}}</p>
            </div>
            <div class="list_item_right">{{list.description_map.validity_delta}}</div>
          </section>
        </li>
        <!-- 红包页脚 -->
        <footer class="hongbao_list_footer">
          <p>限品类：快餐便当、特色菜系、小吃夜宵、甜品饮品、异国料理</p>
        </footer>
      </ul>
    </section>

    <!-- 查看历史红包 -->
    <router-link to="/historyred">
        <section class="history_hongbao">
            <span>查看历史红包</span>
            <i class="el-icon-arrow-right"></i>
        </section>
    </router-link>


    <!-- 兑换红包页脚 -->
    <footer class="hongbao_footer">
        <router-link to="/exchange" class="footer_style">兑换红包</router-link>
        <router-link to="/recommend" class="footer_style">推荐有奖</router-link>
    </footer>
  </div>
</template>
<script>
export default {
  name: "redPacket",
  data() {
    return {
      lists: "",
      indexs: ""
    };
  },
  created() {},
  methods: {}
};
</script>

<style scoped>
/* 红包列表 */
.hongbao_container {
  padding: 0 0.164rem;
}
.hongbao_title {
  display: flex;
  justify-content: space-between;
  font-size: 0.117rem;
  line-height: 0.46875rem;
}
.hongbao_num {
  color: #666;
}
.hongbao_num span {
  color: #ff5340;
}

/* 红包说明 */
.hongbao_explain {
  display: flex;
  align-items: center;
}
.hongbao_explain i {
  font-size: 0.14rem;
  margin-right: 0.05rem;
  color: #3190e8;
}
.hongbao_explain span {
    color: #3190e8;
}

/* 列表 */
.hongbao_list_li {
  background: #fff url("../../../../../assets/item.png") repeat-x;
  background-size: 0.117rem 0.046875rem;
  margin-bottom: 0.117rem;
  border-radius: 0.05859rem;
}
.list_item {
  display: flex;
  justify-content: space-between;
  padding: 0.26rem 0.117rem 0.21rem;
}

/* 可用红包列表左 */
.list_item_left {
  font-size: 0;
  border-right: 0.025rem dotted #ccc;
  flex: 1;
}
.list_item_left span:first-of-type {
  font-size: 0.17578rem;
  color: #ff5340;
  font-weight: 700;
}
.list_item_left span:nth-of-type(2) {
  font-size: 0.35156rem;
  color: #ff5340;
}
.list_item_left span:nth-of-type(3),
.list_item_left span:nth-of-type(4) {
  font-size: 0.1875rem;
  color: #ff5340;
  font-weight: 700;
}
.list_item_left p {
  font-size: 0.09375rem;
  color: #999;
  line-height: 0.16rem;
}

/* 中 */
.list_item_con {
  flex: 2;
  margin-left: 0.35156rem;
}
.list_item_con h4 {
  font-size: 0.164rem;
  color: #666;
  margin-left: -0.164rem;
  line-height: 0.21rem;
}
.list_item_con p {
  list-style-type: disc;
  margin-left: -0.164rem;
  font-size: 0.09375rem;
  color: #999;
  line-height: 0.16rem;
}

.list_item_right {
  font-size: 0.17578rem;
  color: #ff5340;
}

/* 红包页脚 */
.hongbao_list_footer {
  background-color: #f9f9f9;
  padding: 0.1rem;
  border-bottom-left-radius: 0.06rem;
  border-bottom-right-radius: 0.06rem;
  padding: 0 0.117rem 0.21rem;
}
.hongbao_list_footer p {
  font-size: 0.09375rem;
  color: #999;
  margin-left: 0.09375rem;
  line-height: 0.16rem;
}

/* 历史红包 */
.history_hongbao {
    padding: .117rem 0 0.656rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.history_hongbao span {
    font-size: .117rem;
    color: #999;
    margin-right: .05rem;
}

/* 兑换红包页脚 */
.hongbao_footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #fff;
    display: flex;
}
.footer_style {
    flex: 1;
    line-height: 0.46875rem;
    text-align: center;
    font-size: .164rem;
    color: #555;
}
</style>



