<template>
    <div class="resetpassword animated fadeIn">
        <warn></warn>
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>重置密码</div>
        </header>

        <form class="reset_form">
            <section class="input_container phone_num">
                <input type="text" placeholder="账号" v-model="username">
            </section>
            <section class="input_container">
                <input type="text" placeholder="旧密码" v-model="oldpassWord">
            </section>
            <section class="input_container">
                <input type="text" placeholder="请输入新密码" v-model="newpassword">
            </section>
            <section class="input_container">
                <input type="text" placeholder="请确认密码" v-model="confirmpassword">
            </section>

            <section class="input_container">
                <input type="text" placeholder="验证码" v-model="codeNum">

                <div class="img_change_img">
                    <img :src="code&&code" alt="">
                    <div class="change_img" @click="codeBtn()">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
        </form>



        <div class="btn_div" @click="reset()">确认修改</div>
    </div>
</template>

<script>
import warn from '../../../../public/warn'

export default {
    name:"resetpassword",
    data() {
        return {
            username:"",
            oldpassWord:"",
            newpassword:"",
            confirmpassword:"",
            code:"",
            codeNum:""
        }
    },
    created() {
        this.codeBtn()
    },
    methods: {
        back1(){
            this.$router.go(-1)
        },
        reset(){
            const url = "https://elm.cangdu.org/v2/changepassword"
            this.$http({
                method:"post",
                url:url,
                data:{
                    username:this.username,
                    oldpassWord:this.oldpassWord,
                    newpassword:this.newpassword,
                    confirmpassword:this.confirmpassword,
                    captcha_code:this.codeNum
                },
                withCredentials:true
            }).then((res) => {
                console.log(res.data)
                this.$store.state.erroMsg = res.data.message;
                this.$store.state.flag = true;
            })
        },
        codeBtn(){
            const url = "https://elm.cangdu.org/v1/captchas"
            this.$http({
                method:"post",
                url:url,
                withCredentials:true
            }).then((res) => {
                console.log(res)
                if (res.status == 200) {
                    this.code = res.data.code
                }
            })
        }
    },
    components:{
        warn
    }
}
</script>

<style scoped>
.resetpassword {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}
.animated {
  animation-duration: 1s;
}
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}


/* 表单 */
.reset_form {
    margin-top: 0.597rem;
    background-color: #fff;
}

.input_container {
    display: flex;
    justify-content: space-between;
    padding: .14rem .1875rem;
    border-bottom: 1px solid #f1f1f1;
}
.phone_num {
    padding: .07rem .1875rem;
}

.input_container input {
    font-size: .164rem;
    line-height: 0.21rem;
    color: #666;
    border: 0 none;       /*去掉边框*/
    outline-style: none;  /*去掉轮廓线*/
}


.reset_form .input_container:last-child {
    padding: 0.077rem .1875rem;
}


.img_change_img {
    display: flex;
    align-items: center;
}
.img_change_img img {
    width: 0.8203125rem;
    height: 0.3516rem;
    margin-right: .046875rem;
}
.change_img {
    display: flex;
    flex-wrap: wrap;
    width: 0.46875rem;
    justify-content: center;
}
.change_img p {
    font-size: .1289rem;
    color: #666;
}
.change_img p:nth-of-type(2) {
    color: #3b95e9;
    margin-top: .046875rem;
}



.btn_div {
    margin: 0.234375rem .117rem;
    font-size: .164rem;
    color: #fff;
    background-color: #4cd964;
    padding: .117rem 0;
    border: 1px;
    border-radius: .035156rem;
    text-align: center;
}
</style>



