<template>
  <div class="shopInfo">
    <headZj></headZj>
    <div class="shopIntro">
      <div class="active mtop">
        <header>活动与属性</header>
        <div class="clearInfo">
          <div class="cheapInfo mbot">
            <span v-for="(cheap,index) in activities" :key="index">
              <span class="cheapName">{{cheap.icon_name}}</span>
              <span class="sameMg">{{cheap.description}}(APP专享)</span>
            </span>
          </div>
          <ul class="promise">
            <li v-for="(word,index) in $store.state.shopInfo.supports" :key="index">
              <span class="haveCol">{{word.icon_name}}</span>
              <span class="sameMg">{{word.description}}</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="stareFood mtop">
        <header>
          食品安全监督与公示
          <i class="fa fa-angle-right fa-2x"></i>
          <span>企业认证详情</span>
        </header>
        <div class="laughFace">
          <i class='fa fa-user-circle fa-3x'></i>
          <div class="tellSb">
            <p>监督检查结果：<span>差</span></p>
            <p>检查日期</p>
          </div>
        </div>
      </div>
      <div class="shopInfo mtop">
        <header>商家信息</header>
        <ul class="shopInfoShow">
          <li>
            <span>{{$store.state.shopInfo.name}}</span>
          </li>
          <li>
            <span>地址：{{$store.state.shopInfo.address}}</span>
          </li>
          <li>
            <span>营业时间：[{{$store.state.shopInfo.opening_hours[0]}}]</span>
          </li>
          <li>
            <span>营业执照</span>
            <i class="fa fa-angle-right fa-2x"></i>
          </li>
          <li>
            <span>餐饮服务许可证</span>
            <i class="fa fa-angle-right fa-2x"></i>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import headZj from "../../../public/header/headZj";
export default {
  name: "shopInfo",
  data(){
    return{
      activities:""
    }
  },
  components: {
    headZj
  },
  created() {

    this.$store.state.wanted = "商家详情";
    this.activities = this.$store.state.shopInfo.activities;
  }
};
</script>
<style scoped>
.shopIntro {
  padding-top: 0.55rem;
  background: #f5f5f5;
  height: 10rem;
}
.mtop {
  margin-bottom: 0.1rem;
  background: white;
}
header {
  padding: 0 0.14rem;
  border: 0.01rem solid #f5f5f5;
  line-height: 0.42rem;
  margin-bottom: 0.07rem;
  color: #333;
  font-size: 0.18rem;
}
.clearInfo {
  padding-left: 0.14rem;
  padding-right: 0.14rem;
}
.cheapInfo {
  line-height: 0.31rem;
}
.mbot {
  margin-bottom: 0.04688rem;
  color: #666;
}
.cheapName {
  background-color: rgb(240, 115, 115);
  color: white;
}
.sameMg {
  margin-left: 0.04rem;
  font-size: .13rem;
}

.promise li:nth-child(2) .haveCol {
  background-color: rgb(87, 169, 255);
}
.promise li .haveCol {
  background-color: rgb(153, 153, 153);
}
.promise li {
  line-height: 0.31rem;
  color: #666;
}
.stareFood header{
  font-size: 0.16rem;
}
.stareFood header span {
  float: right;
  color: #bbb;
  
  
}
.fa-angle-right {
  float: right;
  margin-top: 0.035rem;
  margin-left: 0.1rem;
  color: #bbb;
}
.shopInfoShow li{
  border-bottom:0.005rem solid #f5f5f5;
      color: #666;
      padding:0.16rem 0;
      font-size: 0.1rem;
}
.shopInfoShow span{
  padding:0 0.14rem;
}
.shopInfoShow i{
  margin-right:0.14rem;
  margin-top:-0.035rem;
}
.tellSb{
  display: inline-block;
  margin-left:0.14rem;
  font-size: .15rem;
}
.laughFace{
    padding:0.14rem 0.14rem;
}
.tellSb p{
  height: 0.31rem;
  color:#666;
}
.tellSb span{
  color:red;
}
.fa-user-circle{
   vertical-align: top;
}
</style>



