<template>
  <div class="account animated fadeInRight">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>账户信息</div>
    </header>

    <!-- 信息列表 -->
    <section class="account_info">
      <!-- 头像部分 -->
      <section class="headportrait">
        <input type="file" class="uploading">
        <h2>头像</h2>
        <div class="headportrait_div">
          <img :src="imgurl" alt>
          <i class="el-icon-arrow-right arrow-rightt-top"></i>
        </div>
      </section>

      <!-- 用户名列表 -->
      <router-link to="/rename">
        <section class="username_list">
          <h2>用户名</h2>
          <div class="user_list_div">
            <p>{{username}}</p>
            <i class="el-icon-arrow-right arrow-rightt-top"></i>
          </div>
        </section>
      </router-link>

      <!-- 收货地址 -->
      <router-link to="/addressed">
        <section class="username_list username_listtwo">
          <h2>收货地址</h2>
          <div class="user_list_div">
            <i class="el-icon-arrow-right"></i>
          </div>
        </section>
      </router-link>

      <section class="account_info_text">账号绑定</section>

      <!-- 手机列表 -->
      <section class="username_list username_listtwo" @click="btn()">
        <h2>
          <i class="el-icon-mobile-phone"></i>
          手机
        </h2>
        <div class="user_list_div">
          <i class="el-icon-arrow-right"></i>
        </div>
      </section>

      <!-- 安全设置 -->
      <section class="account_info_text">安全设置</section>

      <!-- 修改登录密码 -->
      <router-link to="/resetpassword">
        <section class="username_list username_listtwo">
          <h2>登录密码</h2>
          <div class="user_list_div">
            <p>修改</p>
            <i class="el-icon-arrow-right arrow-rightt-top"></i>
          </div>
        </section>
      </router-link>

      <!-- 退出登录 -->
      <section class="log_out" @click="displays= !displays">退出登录</section>

      <!-- 退出弹框 -->
      <section class="exit_list" v-show="displays">
        <section class="list_color"></section>
        <section class="exit_con animated bounceIn">
          <div class="cricle_logo">
            <span></span>
            <span></span>
          </div>
          <h2>是否退出登录</h2>
          <div class="list_bottom">
            <button @click="displays= !displays">再等等</button>
            <div class="get_out">
              <button class="affirm" @click="requests()">退出登录</button>
            </div>
          </div>
        </section>
      </section>
    </section>
  </div>
</template>

<script>
import imgurl from "../../../../assets/picture.png";
import warn from "../../../public/warn";

export default {
  name: "account",
  data() {
    return {
      imgurl,
      username: "",
      displays: false
    };
  },
  created() {
    this.username = this.$store.state.userInfo.username;
    // this.picture()
  },
  components: {
    warn
  },
  methods: {
    back1() {
      this.$router.push({ name: "personal" });
    },
    btn() {
      this.$store.state.erroMsg = "请在手机APP中设置";
      this.$store.state.flag = true;
    },
    requests() {
      const url = "https://elm.cangdu.org/v2/signout";
      this.$http({
        method: "get",
        url: url
      }).then(res => {
        console.log(res);
        this.$store.state.userInfo = "";
        this.$router.push({ name: "personal" });
      });
    }
  }
};
</script>

<style scoped>
.account {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}
.animated {
  animation-duration: 0.5s;
}
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 信息列表 */
.account_info {
  width: 100%;
  margin-top: 0.557rem;
}
/* 头像部分 */
.headportrait {
  /* width: 100%; */
  height: 0.7166rem;
  padding: 0 0.1rem;
  border-top: 1px solid #ddd;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.uploading {
  width: 100%;
  height: 0.7166rem;
  display: block;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
}
.headportrait h2 {
  font-size: 0.14rem;
  color: #333;
  font-weight: 500;
  display: flex;
  align-items: center;
}

.headportrait_div img {
  width: 0.46875rem;
  height: 0.46875rem;
  border-radius: 50%;
  vertical-align: middle;
}

/* 用户名列表 */
.username_list {
  margin-top: 0;
  padding: 0.0744rem 0.095rem;
  align-items: center;
  border-top: 1px solid #ddd;
  background: #fff;
  display: flex;
  justify-content: space-between;
}
.username_list h2 {
  font-size: 0.14rem;
  color: #333;
  font-weight: 500;
  display: flex;
  align-items: center;
  vertical-align: middle;
}

.user_list_div {
  display: flex;
  justify-content: left;
  line-height: 0.3257rem;
}
.user_list_div p {
  text-align: left;
  line-height: 0.3257rem;
  font-size: 0.164rem;
  color: #888;
  margin-right: 0.05rem;
  font-weight: 100;
}
.username_listtwo {
  line-height: 0.33rem;
}

/* 账号绑定文字 */
.account_info_text {
  padding: 0.12rem 0.1rem;
  font-size: 0.117rem;
  color: #666;
}

/* 手机列表 */
.el-icon-mobile-phone {
  font-size: 0.15rem;
  margin-right: 0.1rem;
  padding: 0.02rem;
  background: #09f;
  color: white;
}
.arrow-rightt-top {
  margin-top: 0.05rem;
}

/* 退出登录 */
.log_out {
  width: 96%;
  margin: 0.31rem auto 0;
  line-height: 0.35rem;
  border-radius: 5px;
  text-align: center;
  background: #d8584a;
  font-size: 0.14rem;
  color: #fff;
}

/* 退出弹框 */
.exit_list,
.list_color {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  /* display: none; */
}
.list_color {
  background: #000;
  z-index: 100;
  opacity: 0.2;
}
.exit_con {
  width: 3.185rem;
  background: #fff;
  padding: 0.17rem;
  position: absolute;
  top: 20%;
  left: 3%;
  z-index: 1000;
  border-radius: 5px;
}

.cricle_logo {
  width: 0.9rem;
  height: 0.9rem;
  border: 0.04rem solid #f8bb86;
  border-radius: 50%;
  margin: 0.2rem auto;
  position: relative;
}

.cricle_logo span:first-child {
  width: 0.05rem;
  height: 0.47rem;
  border-radius: 0.02rem;
  background: #f8bb86;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0.1rem;
}
.cricle_logo span:last-child {
  width: 0.07rem;
  height: 0.07rem;
  border-radius: 50%;
  background: #f8bb86;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0.1rem;
}

.exit_con h2 {
  width: 100%;
  text-align: center;
  font-size: 0.3rem;
  color: #575757;
  font-weight: 500;
  margin: 0.25rem 0;
}

.list_bottom {
  width: 100%;
  text-align: center;
}
.list_bottom button:first-child {
  background: #c1c1c1;
  margin-right: 0.09375rem;
}
.list_bottom button {
  display: inline-block;
  padding: 0.09375rem 0.234375rem;
  border-radius: 0.05rem;
  font-size: 0.14rem;
  color: #fff;
  letter-spacing: 1px;
  margin-top: 0.26rem;
}

.get_out {
  display: inline-block;
}

.list_bottom .get_out .affirm {
  background: #dd6b55;
}
</style>



