<template>
  <div class="discounts">
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>我的优惠</div>
    </header>

    <!-- title部分 -->
    <section class="discounts_con">
      <section class="con_title">
        <router-link v-for="(list,index) in lists" :key="index" :to="list.site">
          <span class="title_span" :class="{choosed:changeCut == index}" @click="spanBtn(index)">{{list.name}}</span>
        </router-link>
      </section>
      <router-view></router-view>
    </section>
  </div>
</template>
<script>
export default {
  name: "discounts",
  data() {
    return {
      lists:[
        {name:"红包",site:"/redPacket"},
        {name:"商家代金券",site:"/voucher"}
      ],
      changeCut:0
    };
  },
  created() {},
  methods: {
    back1() {
      this.$router.push("/personal");
    },
    spanBtn(index){
      this.changeCut = index;
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.46rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.46rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* title部分 */
.discounts_con {
  margin-top: 0.46rem;
}

.con_title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 0.46875rem;
  background: white;
}

.title_span {
  text-align: center;
  font-size: 0.1523rem;
  padding-bottom: 0.047rem;
  color: #666;
  border-bottom: 0.0235rem solid #fff;
}

/* 选择样式 */
.choosed {
  border-bottom-color: #3190e8;
  color: #3190e8;
}
</style>



