<template>
      <div class="head">
      <div class="last" @click="goLast()">
        <i class="fa fa-angle-left fa-2x"></i>
      </div>
      <span class="code">{{$store.state.wanted}}</span>
    </div>
</template>
<script>
export default {
    name:'headZj',
    methods:{
      goLast() {
      this.$router.go(-1);
    },
    }
}
</script>
<style scoped>

.last {
  float: left;
  padding-left: 0.14rem;
}
.head {
  width: 100%;
  height: 0.457rem;
  background: #3190e8;
  line-height: 0.457rem;
  text-align: center;
  color: white;
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  z-index:9999;
}
.code {
  font-size: 0.2rem;
  font-weight: bold;
  margin-left: -0.3rem;
}
</style>




