  padding-left: 0.164rem;
}
.question_title {
  padding: 0 0.164rem;
  line-height: 0.46875rem;
  border-bottom: 1px solid #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.question_title span {
  font-size: 0.14rem;
  color: #666;
}
.question_title i {
  font-size: 0.1406rem;
}
</style>



