<template>
  <div class="saidvoucher">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>代金券说明</div>
    </header>

    <!-- content内容 -->
    <div class="markdown">
      <h3>Q1: 什么是商家代金券？</h3>
      <p>商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</p>
      <h3>Q2: 怎么获得商家代金券？</h3>
      <p>进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。</p>
      <p>「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。</p>
      <h3>Q3: 商家代金券使用条件</h3>
      <p>商家代金券仅限在指定商家使用</p>
      <p>商家代金券仅限在线支付使用</p>
      <p>每个订单只能使用一张商家代金券，不可与其他代金券叠加使用</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "saidvoucher",
  data() {
    return {};
  },
  created() {},
  methods: {
    back1() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.saidvoucher {
    background: white;
}

/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* content内容 */
.markdown {
    margin-top: 0.457rem;
    font-size: .14rem;
    color: #666;
    padding: 0 .117rem .117rem;
}
.markdown h3 {
    line-height: 0.46875rem;
    color: #333;
}
.markdown p {
    font-size: .14rem;
    color: #666;
    line-height: 0.234375rem;
}
</style>



