<template>
  <div class="evalute">
    <div class="topEval">
      <div class="topLeft">
        <p class='foodScore'>{{new Number(mark.food_score).toFixed(1)}}</p>
        <p>综合评价</p>
        <p>高于周边商家{{new Number(mark.compare_rating).toFixed(2)*100}}%</p>
      </div>
      <div class="topRight">
        <p>
          <span>服务态度:</span>
         <el-rate
  v-model="serviceScore"
  disabled
  text-color="#ff9900"
  score-template="{value}">
</el-rate>
          <span class="score">{{new Number(serviceScore).toFixed(1)}}</span>
        </p>

        <p>
          <span>菜品评价:</span>
        <el-rate
  v-model="foodScore"
  disabled

  text-color="#ff9900"
  score-template="{value}">
</el-rate>
          <span class="score">{{new Number(foodScore).toFixed(1)}}</span>
        </p>
        <p>送达时间:<span class="reachTime">{{mark.deliver_time}}分钟</span></p>
     
      </div>
    </div>
    <div class="botEval">
      <div class="botTop">
        <ul class="botUl">
          <li v-for="(kind,index) in evaKinds" :key="index" @click="change(index)"
            :class="{'active': index == active}">{{kind.name}}({{kind.count}})</li>
        </ul>
      </div>
      <div class="botBot">
        <ul class="bbUl">
          <li v-for="(info,index) in evaInfo" :key="index" class="bbli">
            <div class="headImg">
              <img src>
            </div>
            <div class="wordRight">
              <p>{{info.username}}</p>
              <p>
                <el-rate
  v-model="info.rating_star"
  disabled
 
  text-color="#ff9900"
 >
</el-rate>
                <span class='scoreBot'>{{info.time_spent_desc}}</span>
              </p>
              <ul class="foodLis">
                <li v-for="(site,index) in info.item_ratings" :key="index">
                  <img
                    class="foodImg"
                    :src="'https://fuss10.elemecdn.com/'+ 
     site.image_hash.slice(0,1) + '/' + site.image_hash.slice(1,3)+'/'+ site.image_hash.slice(3)+'.jpeg'"
                    v-if='site.image_hash!=""'
                  >
                  
                  <span class="foodName" v-if="site.image_hash!=''">{{site.food_name}}</span>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "evalute",
  data() {
    return {
      mark: "",
      foodScore: "",
      serviceScore: "",
      evaInfo: "",
      evaKinds: "",
        active: 0,
    };
  },
  created() {
    this.getEva();
  },
  methods: {
       change(index){
     this.active = index;
    },
    getEva() {
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/ugc/v2/restaurants/" +
          this.$store.state.shopInfo.id +
          "/ratings/scores"
      })
        .then(result => {
          console.log(result);
          this.mark = result.data;
          this.serviceScore = this.mark.service_score;
          this.foodScore = this.mark.food_score;
        })
        .catch(err => {});
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/ugc/v2/restaurants/" +
          this.$store.state.shopInfo.id +
          "/ratings"
      })
        .then(result => {
          console.log(result.data);
          this.evaInfo = result.data;
      
        })
        .catch(err => {});
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/ugc/v2/restaurants/" +
          this.$store.state.shopInfo.id +
          "/ratings/tags"
      })
        .then(result => {
          this.evaKinds = result.data;
          console.log(result.data);
        })
        .catch(err => {});
    },
   
  }
};
</script>
<style scoped>
.reachTime{
    font-size:0.12rem;
    margin-left:0.05rem;
    color:#666;
}
.botUl  .active{
    background: #ededed;
    background-color: #3190e8;
    color: #fff;
}
.el-rate {
  transform: scale(0.5);
}
.foodName {
  overflow: hidden;
  text-overflow: ellipsis;
 
  font-size: 0.12rem;
}
.foodLis li {
  width: 0.7rem;
  float: left;
  margin-right: 0.2rem;
}
.foodImg {
  width: 0.7rem;
  float: left;
}
.timeReach {
  display: inline-block;
}
.wordRight p {
  margin-bottom: 0.07rem;
}

.bbli {
  width: 3.51rem;
  padding: 0.14rem;
  border-top: 0.01rem solid #f5f5f5;
  overflow: hidden;
}
.evalute {
  z-index: 999;
  background: grey;
}
.topEval {
  background-color: #fff;
  padding: 0.1875rem 0.11719rem;
  margin-bottom: 0.11719rem;
  overflow: hidden;
}
.topLeft p{
    margin-bottom: 0.11rem;
}

.foodScore{
    color:#f60;
}
.topLeft {
  width: 1.5076rem;

  float: left;
  text-align: center;
}
.el-rate {
  display: inline-block;
  margin-left: -0.3rem;
}
.score {
  font-size: 0.14rem;
  margin-left: -0.3rem;
  color:#f60;
}
.scoreBot{
      font-size: 0.14rem;
  margin-left: -0.2rem;
}
.topRight p{
    margin-bottom:0.07rem;
}
.topRight {
  float: left;
  margin-left: 0.1rem;
  width:1.8rem;
}
.botEval {
  background: #fff;
  overflow: hidden;
  padding-left: 0.14rem;
  padding-top: 0.1rem;
}

.botUl li {
  float: left;
  padding: 0.07rem;
  background: #ebf5ff;
  margin: 0 0.09rem 0.04388rem 0;
  border-radius: 0.05rem;
  font-size: 0.14rem;
  color: #6d7885;
}
.botTop {
  padding-bottom: 0.1rem;
  overflow: hidden;
}
.botBot {
  overflow: hidden;
}
.evalute {
  flex: 4;
  overflow-y: auto;
  overflow-x: hidden;
  background: #ededed;
  color: #666;
}
.evalute::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}
</style>




