<template>
  <div class="search" v-cloak>
    <div class="head">
      <div class="last" @click="goLast()">
        <i class="el-icon-arrow-left"></i>
      </div>
      <span class="cityName">{{name}}</span>
      <span class="changeCity">
        <router-link to="/">切换城市</router-link>
      </span>
    </div>
    <div class="searchInput">
      <div class="buildInput">
        <input type="text" placeholder="输入学校，商务楼，地址" class="cityInput" v-model="search">
        <i class="el-icon-error" v-show="search!=''" @click="emptyInput()"></i>
      </div>
      <div class="btn">
        <input type="submit" value="提交" class="sub" @click="searchPlace()">
      </div>
    </div>
    <div class="searchHistory" v-show="sureSearch=='' && $store.state.searchHistory!=''">
      <span>搜索历史</span>
    </div>
    <ul class="searchShow" v-if="sureSearch!=''">
      <li v-for="(aid,index) in aids" :key="index" @click="goIndex(aid)">
        <h4>{{aid.name}}</h4>
        <p>{{aid.address}}</p>
      </li>
    </ul>
    <ul class="historyShow" v-else>
      <li v-for="(his,index) in $store.state.searchHistory" :key="index" @click="goIndex(his)">
        <h4>{{his.name}}</h4>
        <h4>{{his.address}}</h4>
      </li>
      <li class="emptyHistory" @click="emptyHistory()" v-if="$store.state.searchHistory!=''">
        <span>清空所有</span>
      </li>
    </ul>
  </div>
</template>
<script>
import { mapMutations } from "vuex";
export default {
  name: "search",
  data() {
    return {
      id: "",
      name: "",
      search: "",
      aids: "",
      sureSearch: "",
      flag: true
    };
  },
  created() {
    this.id = localStorage.getItem("cityId");
    this.name = localStorage.getItem("cityName");
  },
  methods: {
    goLast() {
      this.$router.go(-1);
    },
    searchPlace() {
      console.log(123);
      this.$http({
        method: "get",
        url: "https://elm.cangdu.org/v1/pois",
        params: {
          city_id: this.id,
          keyword: this.search
        }
      })
        .then(result => {
          this.aids = result.data;
          if (this.search == "") return;
          this.sureSearch = this.search;
        })
        .catch(err => {});
    },

    emptyInput() {
      this.search = "";
    },
    emptyHistory() {
      this.$store.state.searchHistory.splice(0);
    },
    goIndex(aid) {
      
      console.log(aid);
      let flag = false;
      for (var i = 0; i < this.$store.state.searchHistory.length; i++) {
        if (this.$store.state.searchHistory[i].name == aid.name) {
          flag = true;
        }
      }
      if (flag == false) {
        this.$store.state.searchHistory.push(aid);
      }
      console.log(aid);
      //历史纪录去重

      localStorage.setItem("latitude", aid.latitude);
      localStorage.setItem("longitude", aid.longitude);
      this.$router.push({
        name: "takeOut",
        query: {
          geohash: aid.latitude + "," + aid.longitude
        }
      });
    }
  }
};
</script>
<style scoped>
a {
  color: white;
  font-weight: bold;
}
.last {
  float: left;
  padding-left: 0.14rem;
}
.head {
  /* width: 100%; */
  height: 0.457rem;
  background: blue;
  line-height: 0.457rem;
  text-align: center;
  color: white;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
}
.changeCity {
  float: right;
  padding-right: 0.14rem;
}
.cityName {
  font-size: 0.18rem;
  font-weight: 700;
  margin-left: 0.45rem;
}
.searchInput {
  margin-top: 0.557rem;
}
.buildInput {
  width: 90%;
  margin: 0 auto;
  text-align: center;
}
.cityInput {
  text-indent: 0.1rem;
  border: 1px solid #e4e4e4;
  width: 100%;
  height: 0.32rem;
  font-size: 0.18rem;
  margin-bottom: 0.1rem;
  color: #333;
}
.btn {
  width: 90%;
  margin: 0 auto;
}
.sub {
  background: #3190e8;
  font-size: 0.16rem;
  color: #fff;
  border-radius: 0.02rem;
  margin: 0 auto 0.15rem;
  width: 100%;
  height: 0.32rem;
  text-align: center;
}
.searchHistory {
  /* width: 100%; */
  height: 0.2rem;
  padding-left: 0.2rem;
  font-size: 0.12rem;
  border-top: 0.01rem solid #e4e4e4;
  border-bottom: 0.01rem solid #e4e4e4;
  line-height: 0.2rem;
}
.searchShow {
  z-index: 2;
  background: white;
  padding-right: 0.2rem;
}
.searchShow li {
  padding-top: 0.15rem;
  border-bottom: 1px solid #e4e4e4;
  /* width: 100%; */
  height: 0.57rem;
  padding-left: 0.2rem;
}
h4 {
  color: #333;
  font-size: 0.14rem;
}
.searchShow li p {
  margin-top: 0.15rem;
  color: #999;
  font-size: 0.14rem;
}
input {
  outline: none;
}
.historyShow li {
  font-size: 0.16rem;
  line-height: 0.25rem;
  padding: 0 0.2rem;
  border-bottom: 0.01rem solid #eee;
}
.el-icon-error {
  position: absolute;
  top: 0.65rem;
  right: 0.3rem;
}
.emptyHistory {
  line-height: 0.5rem;
  text-align: center;
  font-weight: bold;
  /* background: #eee; */
}
</style>



