<template>
    <div class="addressed animated fadeInRight">
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>编辑地址</div>
            <div v-show="!displays" @click="displays= !displays">编辑</div>
            <div v-show="displays" @click="displays= !displays">完成</div>
        </header>

        
        <section class="address_con">
          <ul class="addresslist">
            <li v-for="(list,index) in lists" :key="index">
              <div>
                <p>{{list.name}}</p>
                <p>
                  <span>{{list.phone}}</span>
                </p>
              </div>
              <div class="deletesite">
                <span @click="deletes(index)" v-show="displays">x</span>
              </div>
            </li>
          </ul>

          <router-link to="/increase">
            <div class="addsite">
              <span>新增地址</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </router-link>
        </section>


    </div>
</template>

<script>
export default {
    name:"addressed",
    data() {
        return {
            lists:[],
            displays : false
        }
    },
    created() {
      this.gain()  
    },
    methods: {
        back1(){
            this.$router.push({name:"account"})
        },
        gain(){
          const url = "https://elm.cangdu.org/v1/users/"+ this.$store.state.userInfo.user_id +"/addresses"
          this.$http({
            method:"get",
            url:url
          }).then((res) => {
            console.log(res.data)
            this.lists = res.data
            this.lists.reverse()
          })
        },
        deletes(index){
          const url = "https://elm.cangdu.org/v1/users/"+this.$store.state.userInfo.user_id+"/addresses/"+this.lists[index].id+""
          this.$http({
            method:"DELETE",
            url:url
          }).then((res) => {
            this.lists.splice(index,1)
          })
        },
    },
}
</script>


<style scoped>
.addressed {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}
.animated {
  animation-duration: 0.3s;
}


/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:nth-of-type(2) {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}
.head div:nth-of-type(3),.head div:nth-of-type(4){
  font-size: 0.164rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  right: 0.1rem;
  transform: translate(0, -50%);
}

.address_con {
    width: 100%;
    margin-top: .55075rem;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
}
.addresslist {
  background: #fff;
}
.addresslist li:first-of-type {
  background: #fff8c3;
}
.addresslist li {
    border-bottom: 1px solid #d9d9d9;
    padding: 0.09375rem;
    display: flex;
    justify-content: space-between;
}
.addresslist li p {
    line-height: .2109rem;
    font-size: .14rem;
    color: #333;
    line-height: 0.21rem;
}
.addresslist li p span {
  display: inline-block;
  font-size: .14rem;
  color: #333;
}


.deletesite {
  display: flex;
  align-items: center;
}
.deletesite span {
  /* display: none; */
  font-size: .1875rem;
  color: #999;
}

.addsite {
  margin-top: .09375rem;
    background: #fff;
    padding: .046875rem .09375rem;
    border-top: 1px solid #d9d9d9;
    display: flex;
    justify-content: space-between;
}
.addsite span {
  display: block;
    font-size: .164rem;
    color: #333;
    line-height: 0.328125rem;
}
.addsite i {
  font-size: .16rem;
  line-height: 0.328rem;
}
</style>



