<template>
  <div class="login">
    <warn></warn>
    <headZj></headZj>
    <div class="content">
      <div class="onInput">
        <input type="text" v-model="username" placeholder="账号">
      </div>
      <div class="onInput">
        <input :type="value1?'text':'password'" v-model="password" placeholder="密码">
        <el-switch
          v-model="value1"
          active-color="#13ce66"
          inactive-color="#ff4949"
          class="switchCode"
          @click="showOrhid()"
        ></el-switch>
        <br>
      </div>
      <div class="onInput three_onInput">
        <input type="text" v-model="codeNumber" placeholder="验证码">
        <div class="code_img">
          <img :src="code&&code" alt>
          <div @click="getCode()" class="next">
            <p>看不清</p>
            <p class="p2">换一张</p>
          </div>
        </div>
        <br>
      </div>
    </div>
    <p class="notice">温馨提示：未注册过的账号，登陆时将自动注册</p>
    <p class="notice">注册过的用户可凭账号密码登陆</p>

    <div class="btn" @click="login()">登陆</div>
    <div class="resetCode">
      <router-link :to="{name:'resetCode'}" class="p2">重置密码?</router-link>
    </div>
  </div>
</template>
<script>
import warn from "../public/warn";
import headZj from "../public/header/headZj";
export default {
  name: "login",
  data() {
    return {
      lanou: 0,
      code: "",
      username: "",
      password: "",
      codeNumber: "",
      value1: false
    };
  },
  components: {
    warn,
    headZj
  },
  created() {
    this.getCode();
    this.$store.state.wanted = "密码登陆";

    if (this.$route.params.bj) {
      this.lanou = this.$route.params.bj;
    }
  },
  methods: {
    getCode() {
      const url = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        method: "post",
        url: url,
        withCredentials: true
      }).then(res => {
        if (res.status == 200) {
          this.code = res.data.code;
        }
      });
    },
    login() {
      if (this.username == "") {
        this.$store.state.erroMsg = "用户名为空";
        this.$store.state.flag = true;
        return false;
      } else if (this.password == "") {
        this.$store.state.erroMsg = "密码为空";
        this.$store.state.flag = true;
        return false;
      } else if (this.codeNumber == "") {
        this.$store.state.erroMsg = "验证码为空";
        this.$store.state.flag = true;
        return false;
      } else {
        const api = "https://elm.cangdu.org/v2/login";
        this.$http({
          method: "post",
          url: api,
          data: {
            username: this.username,
            password: this.password,
            captcha_code: this.codeNumber
          },
          withCredentials: true
        }).then(res => {
          console.log(res);
          if (!res.data.message) {
            this.$store.state.userInfo = res.data;
            if (this.lanou != 0) {
              this.$router.go(-1);
            } else {
              this.$router.push({
                name: "home"
              });
            }
          } else {
            (this.$store.state.erroMsg = res.data.message),
              (this.$store.state.flag = true);
          }
        });
      }
    }
  }
};
</script>
<style scoped>
.login {
  height: 6.67rem;
  background: #f5f5f5;
}
.content {
  margin-top: 0.597rem;
  background: white;
}

.onInput {
  display: flex;
  justify-content: space-between;
  padding: 0.14rem 0.1875rem;
  border-bottom: 0.01rem solid #f1f1f1;
}
.onInput input {
  font-size: 0.164rem;
  color: #666;
  line-height: 0.19rem;
}

.switchCode {
  display: flex;
  justify-content: center;
  padding: 0 0.046875rem;
  position: relative;
  left: 0.4rem;
}

.three_onInput {
  height: 0.226rem;
}

.code_img {
  display: flex;
  align-items: center;
}
.code_img img {
  width: 0.82rem;
  height: 0.35156rem;
  margin-right: 0.046875rem;
}

.next {
  /* display: flex;
  flex-wrap: wrap; */
  width: 0.46875rem;
  /* justify-content: center; */
}
.next p {
  font-size: 0.1289rem;
  color: #666;
}
.next p:last-child {
  color: #3b95e9;
  margin-top: 0.046875rem;
}

.notice {
  font-size: 0.117rem;
  color: red;
  padding: 0.09375rem 0.14rem;
  line-height: 0.117rem;
}

.btn {
  margin: 0 0.117rem 0.234375rem;
  font-size: 0.164rem;
  color: #fff;
  background-color: #4cd964;
  padding: 0.115rem 0;
  border: 1px;
  border-radius: 0.035rem;
  text-align: center;
}

.p2 {
  float: right;
  font-size: 0.14rem;
  color: #3b95e9;
  margin-right: 0.07rem;
}
</style>



