<template>
  <div class="classify animated fadeIn">
    <headZj></headZj>

    <div class="nav">
      <div :class='show==0 && flag?"bg":""' @click.stop="show=0; flag=!flag;flag1=flag2=false">


        {{title}}
        <span>
          <!-- <i :class="[rotate?'fa fa-caret-down go':'fa fa-caret-down aa']"></i> -->
        </span>
      </div>
      <div :class='show==1 && flag1?"bg":""'  @click.stop="show=1;flag1=!flag1;flag=flag2=false" class="divMid">
        排序
              <!-- <i :class="[rotate1?'fa fa-caret-down go':'fa fa-caret-down aa']"></i> -->
      </div>
      <div :class='show==2 && flag2?"bg":""'  @click.stop="show=2;flag2=!flag2;flag=flag1=false">
        筛选
             <!-- <i :class="[rotate2?'fa fa-caret-down go':'fa fa-caret-down aa']"></i> -->
      </div>
    </div>

    <!-- one -->

 <div class='zz' v-if="show==0 && flag">
      <div class="show" >
       
         
        <ul class="showLeft" @mousewheel.prevent="null" >
          <li
            v-for="(list,index) in shopLists "
            :key="index"
            @click="qdFirst(index)"
            :class="[index==active?'active':'']"
          >
            <img
              v-if="index==4"
              :src="'https://fuss10.elemecdn.com/'+ 
     list.image_url.slice(0,1) + '/' + list.image_url.slice(1,3)+'/'+ list.image_url.slice(3)+'.jpeg'"
              alt
            >
            <img v-else-if="index==0" src alt>
            <img v-else-if="index == 7" :src="
    defaultImgA " alt class="shanghu">
            <img
              v-else
              :src="'https://fuss10.elemecdn.com/'+ 
     list.image_url.slice(0,1) + '/' + list.image_url.slice(1,3)+'/'+ list.image_url.slice(3)+'.png'"
              alt
            >
            <span>{{list.name}}</span>
            <span class="flNumb frCount">{{list.count}}

            </span>
            <i class="fa fa-angel-right"></i>
          </li>
        </ul>
        <div class='showRr'>
        <div class="showRight"  >
        
          <ul v-if="indexYa!=''">
            <li
              v-for="(site,index) in shopLists[indexYa].sub_categories"
              :key="index"
              @click="changeYa(index,site)"
              :class="[index==ay?'ay':'']"
            >
              {{site.name}}
           <span class='frCount'>{{site.count}}</span>
            </li>
          </ul>
          </div>
        </div>
      </div>
      </div>
        <div class='zz' v-if="show==1 && flag1">
    <div class="showYa" >
      <ul>
        <li v-for="(name,index) in pxYa" :key="index" @click="px(name.id)">
          <span>{{index}}</span>
          {{name.name}}
        </li>
      </ul>
    </div>
        </div>
    <div class='zz' v-if="show==2 && flag2">
    <div class="showYa2" >
      <div class="showYaTop">
        <span class="span1">配送方式</span>
        <div class="divBird">
          <el-checkbox v-model="checked">蜂鸟专送</el-checkbox>
        </div>
      </div>
      <div class="showYaMid">
        <span class="span1">商家属性（可以多选）</span>

        <el-checkbox-group v-model="checkList" class="sjbz">
          <el-checkbox v-for="(xx,index) in payWay" :key="index" border :label="xx.id">{{xx.name}}</el-checkbox>
        </el-checkbox-group>

        <div class="birdBot">
          <div class="div1" @click="checked=false;checkList=[]">清空</div>
          <div class="div2" @click="goMode()">确定</div>
        </div>
      </div>
    </div>
        </div>


    <div class="shopShow">
      <ul>
        <li v-for="(shop,index) in shops" :key="index" @click="goDetail(shop)">
          <div class="shopLeft">
            <img :src="$store.state.shopBaseUrl + shop.image_path" alt>
          </div>
          <div class="shopRight">
            <div class="shopHead">
              <span class="brand">品牌</span>
              <span class="shopName">{{shop.name}}</span>
              <div class="promise">
                <span v-for="(word,index) in shop.supports" :key="index">{{word.icon_name}}</span>
              </div>
            </div>
            <div class="shopRate">
              <el-rate
                v-model="shop.rating"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}"
              ></el-rate>
              <span class="sale">月售{{shop.recent_order_num}}单</span>
              <div class="promise pro2">
                <span class="pText" v-if="shop.delivery_mode">{{shop.delivery_mode.text}}}</span>
                <span class="pText2">准时到达</span>
              </div>
            </div>
            <div class="shopDistance">
              <p
                class="money"
              >￥{{shop.float_minimum_order_amount}}起送/{{shop.piecewise_agent_fee.tips}}</p>
              <p class="time">
                {{shop.distance}}/
                <span class="timeCol">{{shop.order_lead_time}}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import defaultImg from "../../../../static/img/default.jpg";
import headZj from "../../public/header/headZj";
export default {
  name: "classify",
  data() {
    return {
      rotate: false,
      rotate1:false,
      rotate2:false,
      checked: false,
      checkList: [],
      flag: false,
      flag1: false,
      flag2: false,
      show: "",
      ay: 0,
      shopLists: [],
      active: 0,
      title: "",
      defaultImgA: defaultImg,
      indexYa: 0,
      payWay: "",
      id: "",
      ids: "",
      odby: "",
      supIds: "",
      mode: "",
      delMode: [],
      shops: [],
      shops1: [],
      pxYa: [
        { name: "智能排序", id: 4 },
        { name: "距离最近", id: 5 },
        { name: "销量最高", id: 6 },
        { name: "起送价最低", id: 1 },
        { name: "配送速度最快", id: 2 },
        { name: "评分最高", id: 3 }
      ]
    };
  },
  components: {
    headZj
  },
  created() {
    this.title = this.$route.params.title;
    this.$store.state.wanted = this.title;
    console.log(this.$route.params.title);
    this.getShopList();
    this.getshop(this.id, "", "", "");
  },
  methods: {
      goDetail(shop) {
     this.$store.state.shopInfo = shop;
      this.$router.push({ name: "storeDetail"});
    },
    px(index) {
      this.flag1 = false;
      this.odby = index;
      this.getshop();
    },
    getshop() {
      console.log("first");
      console.log(localStorage.getItem("latitude"));
      this.$http({
        method: "get",
        url: "https://elm.cangdu.org/shopping/restaurants",
        params: {
          latitude: localStorage.getItem("latitude"),
          longitude: localStorage.getItem("longitude"),
          offset: 0,
          limit: 20,
          keyword: "",
          restaurant_category_id: this.id,
          restaurant_category_ids: [this.ids],
          support_ids: this.supIds,
          order_by: this.odby,
          delivery_mode: [this.mode]
        }
      })
        .then(result => {
          console.log(this.id);
          console.log("aaa", result.data);

          this.shops = result.data;
          console.log("1212", this.shops.length);
          if (this.shops.length == 0) {
            this.supIds = "";
            this.mode = "";
            this.supIds = "";
            this.ids = "";
          }
        })
        .catch(err => {});
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/shopping/v1/restaurants/activity_attributes"
      })
        .then(result => {
          this.payWay = result.data;
          console.log(this.payWay);
        })
        .catch(err => {});
    },
    getShopList() {
      console.log(123);
      this.$http({
        method: "get",
        url: "https://elm.cangdu.org/shopping/v2/restaurant/category"
      })
        .then(result => {
          // var jsonData = JSON.stringify(result);
          // var resultData = JSON.parse(jsonData);
          this.shopLists = result.data;
          for (var i = 0; i < this.shopLists.length; i++) {
            if (this.shopLists[i].name == this.title) {
              this.indexYa = i;
              this.active = i;
              this.id = this.shopLists[i].id;
              console.log(this.id);
            } else {
              this.active = 100;
            }
          }
          console.log(this.shopLists);
          this.shopLists[this.indexYa].sub_categories.splice(0, 1);
        })
        .catch(err => {});
    },
    qdFirst(index) {
      if (index == 0) {
        this.$router.go(-1);
      }
      this.active = index;
      this.indexYa = index;
      if (
        this.shopLists[this.indexYa].sub_categories.length > 0 &&
        this.shopLists[this.indexYa].sub_categories[0].count == 0
      ) {
        this.shopLists[this.indexYa].sub_categories.splice(0, 1);
      }
    },
    changeYa(index, site) {
      this.ay = index;
      this.flag = false;
      this.title = site.name;
      this.$store.state.wanted = this.title;
      this.ids = site.id;
      this.getshop();
      console.log(this.id);
    },
    // 商家活动
    goMode() {
      this.flag2 = false;
      if (this.checked == true) {
        this.mode = 1;
      }
      this.supIds = this.checkList;
      console.log(this.checkList);
      this.getshop();
    }

    // 蜂鸟
  }
};
</script>


<style scoped>
.animated {
  animation-duration: 1.5s;
}
 .aa{
        transition: all 0.5s;
    }
    .go{
        transform:rotate(180deg);
        transition: all 0.5s;
    }
.showRight .ay{
   color: #57a9ff;
}
.bg {
  color: #57a9ff;
}
.span1 {
  display: inline-block;
  line-height: 0.3516rem;
  margin-left: 0.14rem;
}
.divBird {
  line-height: 0.3867rem;
  width: 1.1rem;
  margin-left: 0.14rem;
  border: 0.01rem solid #f1f1f1;
  text-align: center;
}
.sjbz {
  overflow: auto;
  zoom: 1;
}
.el-checkbox:nth-child(1) {
  margin-left: 0.1rem;
}

.el-checkbox:nth-child(even) {
  margin-left: -0.1rem;
  margin-right: 0;
}
.el-checkbox {
  width: 1rem;
  margin-bottom: 0.1rem;
}
.birdBot {
  padding: 0.07rem 0.046rem;
  overflow: hidden;
  background: #f1f1f1;
}
.birdBot div {
  line-height: 0.42rem;
  width: 1.7695rem;
  background: white;
  color: #666;
  text-align: center;
}
.div1 {
  float: left;
}
.birdBot .div2 {
  float: right;
  color:white;
  background: #56d176;
}
.active {
  background: white;
}
.nav {
  margin-top: 0.45rem;
  width: 100%;
  overflow: hidden;
  padding: 0.07rem 0;
  border-bottom: 0.005rem solid #e4e4e4;
  z-index: 100;
  position: fixed;
  background: white;
  font-size: 0.14rem;
  color: #333;
}
.nav div {
  line-height: 0.3rem;

  float: left;
  text-align: center;
  width: 33%;
}
.divMid {
  border-right: 0.005rem solid #e4e4e4;
  border-left: 0.005rem solid #e4e4e4;
}
.show {
  height: 3.8rem;
  border-bottom: 0.01rem solid #e4e4e4;
  overflow: hidden;
  position: fixed;
  top: 0.8757rem;
  left: 0;
  right: 0;
  background: white;
  z-index: 25;
}
.frCount{
  float: right;
}
.px {
  background: blue;
  width: 100%;
  height: 2rem;
}
.choice {
  background: green;
  width: 100%;
  height: 2rem;
}
.zz{
  width:100%;
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgba(0, 0, 0, 0.6);
  z-index:20;
}
.showLeft li {
    padding:0 0.11719rem;
    width: 1.582rem;
  border-bottom: 0.005rem solid #e4e4e4;
  /* padding: 0 0.11719rem; */
}
.showLeft img {
  width: 0.1875rem;
}
.showLeft {
  background: #ededed;
  float: left;
  line-height: 0.42rem;
  font-size:0.12rem;
  color:#666;
}
.showRr{
  height: 3.8rem;
    overflow-y:scroll;
}
.showRight {
  width: 1.69rem;
  background: white;
  float: right;
  padding-right: 0.11719rem;

}
.showRight li {
  font-size:0.14rem;
  color:#666;
  line-height: 0.416rem;
  border-bottom: 0.005rem solid #e4e4e4;
}
.shanghu {
  width: 0.48rem;
}
.flNumb {
  color: white;
  font-size: 0.1rem;
  background: black;
  border-radius: 50%;
  line-height: 0.1716rem;
  margin-top:0.15rem;
  background: #ccc;
  border:0.01rem solid #ccc;
}

.shopShow {
  padding-top: 0.85rem;
  overflow-y: auto;
}
.shopShow ul {
  overflow: hidden;
}
.shopShow ul li {
  overflow: hidden;
  padding: 0.117rem 0.0717rem;
}
.shopList {
  background: #fff;
  padding-left: 0.14rem;
}
.shopList li {
  overflow: hidden;
  /* display: flex; */
  border-bottom: 0.005rem solid #f1f1f1;
  padding: 0.16rem 0;
}
.shoper {
  line-height: 0.38rem;
  padding-left: 0.14rem;
  color: #999;
  font-size: 0.14rem;
}
.shopLeft {
  float: left;
  width: 0.72rem;
  height: 0.72rem;
  /* background: red; */
}
.shopLeft img {
  width: 0.6328rem;
}
.shopRight {
  float: right;
  padding-left: 0.05rem;
  width: 2.836rem;
  height: 0.716rem;
}
.shopHead {
  height: 0.1715rem;
  /* background: red; */
}
.shopRate,
.shopDistance,
.shopHead {
  padding-right: 0.14rem;
  width: 2.7rem;
}
.shopRate {
  height: 0.14rem;

  margin-top: 0.12rem;
}
.shopDistance {
  height: 0.16rem;

  margin-top: 0.12rem;
}
.brand {
  width: 0.24rem;
  height: 0.14rem;
  padding: 0, 0.02344rem;
  margin-right: 0.04688rem;
  background: #ffd930;
  font-size: 0.016rem;
  font-weight: bold;
}
.shopName {
  font-weight: bold;
  font-size: 0.15rem;
}
.el-rate {
  transform: scale(0.5);
  margin-left: -0.9rem;
}
.promise {
  float: right;
  font-size: 0.12rem;
  color: #999;
}
.promise span {
  margin-right: 0.005rem;
  border: 0.005rem solid #f1f1f1;
}
.pText {
  background: #57a9ff;
  font-size: 0.01rem;
}
.pText2 {
  font-size: 0.1rem;
}
.pro2 {
  margin-top: -0.2rem;
}
.money {
  transform: scale(0.9);
  float: left;
  font-size: 0.12rem;
  color: #666;
}
.time {
  transform: scale(0.9);
  font-size: 0.12rem;
  color: #666;
  float: right;
}
.timeCol {
  color: #57a9ff;
}
.sale {
  transform: scale(0.2);
  font-size: 0.04rem;
  position: relative;
  left: 0.75rem;
  top: -0.2rem;
}
.el-rate__icon {
  margin-right: 0;
}
.showYa {
  position: fixed;
  top: 0.825rem;
  left: 0;
  right: 0;
  z-index: 12;
}
.showYaTop{
  margin-top:0.05rem;
}
.showYa ul li {
  line-height: 0.5858rem;
  border-bottom: 0.01rem solid #f1f1f1;
  font-size: 0.14rem;
  color: #666;
  background: white;
}
.showYa span {
  width: 0.16rem;
  margin-left: 0.1875rem;
  margin-right: 0.07rem;
}
.showYa2 {
  background: white;
  position: fixed;
  top: 0.825rem;
  left: 0;
  right: 0;
  z-index: 12;
}
</style>


