<template>
  <div class="makeAdd">
    <headZj></headZj>
    <warn></warn>
    <div class="info">
      <div class="name">
        <div class="nameLeft fl">联系人</div>
        <div class="nameRight fl">
          <input type="text" placeholder="你的名字" v-model="$store.state.name">
          <br>
          <el-radio-group v-model="$store.state.sex">
            <el-radio :label="boy?'先生':'女士'">先生</el-radio>
            <el-radio :label="!boy?'先生':'女士'">女士</el-radio>
          </el-radio-group>
        </div>
      </div>
      <div class="phone name">
        <div class="nameLeft fl">联系电话</div>
        <input type="text" placeholder="你的手机号" v-model="$store.state.phone">
      </div>
      <div class="add name">
        <div class="nameLeft fl">送餐地址</div>
        <div class="xxdd nameRight fl">
          <p @click='goSearch()'>
            <input type="text" placeholder="小区/学校/写字楼等"  v-model='$store.state.info.name'>
    
             </p>

          <input type="text" placeholder="详细地址（如门牌号等）" class="xx2" v-model="address_detail">
        </div>
      </div>
      <div class="bq name">
        <div class="nameLeft fl">标签</div>
        <input type="text" placeholder="无/家/学校/公司" v-model="tag">
      </div>
      <div class="button" @click="goAdd()">确定</div>
    </div>
  </div>
</template>
<script>
import headZj from "../../../../public/header/headZj";
import warn from "../../../../public/warn";

export default {
  name: "makeAdd",
  data() {
    return {
      boy: true,
      tag_type: "",
      tag:"",
      address_detail: "",

      phone_bk: "",
      latitude: this.$store.state.guessCityInfo.latitude,
      longitude: this.$store.state.guessCityInfo.longitude,
    };
  },
  created() {
  },
  methods: {
    goSearch(){
      this.$router.push({name:'searchAddress',params:{id:1}})
      console.log(123);
    },
    goAdd() {

      const url =
        "https://elm.cangdu.org/v1/users/" +
        this.$store.state.userInfo.id +
        "/addresses";
      this.$http({
        method: "post",
        url: url,
        data: {
                   user_id: this.$store.state.userInfo.id,
                    address_detail: this.address_detail,
                    geohash: this.$store.state.info.latitude+","+this.$store.state.info.longitude,
                    name: this.$store.state.name,
                    phone: this.$store.state.phone,
                    tag: this.tag,
                    sex: "0",
                    phone_bk: this.phone_bk,
                    address: this.$store.state.info.address,
                    tag_type: 2,
                    tag:"家"
        }
      }).then(res => {
        console.log(res);
        if(res.data.messsage){
          this.$store.state.erroMsg = res.data.messsage;
          this.$store.state.flag = true;
        }else{
          this.$store.state.erroMsg = res.data.success;
        this.$store.state.flag = true;
          // this.$router.push({ name: "getOrder" });
          this.$store.state.go = "/getOrder"
        }
      });
    }
  },
  components: {
    headZj,
    warn
  },
  created() {
    this.$store.state.wanted = "添加地址";
  }
};
</script>
<style scoped>
input {
  outline: none;
}
.info {
  margin-top: 0.457rem;
}
.name {
  padding-top: 0.117rem;
  height: 0.7rem;
  border-bottom: 0.01rem solid grey;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.nameLeft {
  width: 1rem;
  text-align: center;
  margin-right: 0.2rem;
}
.el-radio-group {
  margin-top: 0.3rem;
}
.xx2 {
  margin-top: 0.3rem;
}
.button {
  width: 90%;
  margin: 0.3rem auto 0;
  background: lightgreen;
  text-align: center;
  color: white;
  line-height: 0.31rem;
}
</style>



