export default {
    // 通过 store.commit('方法名',附加参数)，
    // 触发increment，会把state状态接收到
    increment (state,a) {
      // 变更状态 state 参数 接收state状态值
      // state.count++
      //或者
      // this.state.count += a
      //对象
      this.state.count += a.age
    },
    // 不传参数a，则默认值为1
    reduce (state,a=1){
      // 模拟数据请求，假设数据返回需要时间1秒
      //  state.count--;
      //  setTimeout(() => {
         //文档意思：不能同步刷新了
         //测试结果：可以同步刷新
        this.state.count -= a;
      //  }, 1000);
    },
    goLast(){
      console.log(123);
      this.$router.go(-1);
    },
    unique(state,arr) {
      console.log(123);
      console.log(arr);
      // console.log(state.foodSearchHistory);
      var newArr = []; //新建一个数组
      for (var i = 0; i <  arr[0].length; i++) {
        if (newArr.indexOf(arr[0][i]) < 0) {
          //若新数组中未包含该项则将其存入新数组
          newArr.push(arr[0][i]);
        }
      }
     
      // console.log("new:"+arr);
      console.log("str:"+arr[1]);
      state[arr[1]] = newArr;

    },
    confirmThing(state){
    state.flag = false
    console.log(123);
    }
  }


