<template>
  <div class="integralissue">
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>积分问题</div>
    </header>

    <div class="balan_condition">
      <h3>Q1: 怎么获得积分？</h3>
      <p>在线支付的订单将获得订单积分奖励：</p>
      <p>积分将在用户完成评价后获得。</p>
      <p>可获得积分=订单金额×10（即1元=10点积分）。</p>
      <p>订单金额指实际付款金额，不包含活动优惠金额。</p>
      <p>每位用户每天最多可以获得2000积分，体验商家的订单和评价不会增加积分。</p>
      <h3>Q2: 积分用来做什么？</h3>
      <p>可以在积分商城兑换各种礼品。</p>
      <h3>Q3: 礼品兑换很多天了还没有收到，该怎么办？</h3>
      <p>礼品从兑换日起，3个工作日（周末不算）内处理发货，发货后，通常会在3个工作日左右送达。</p>
      <h3>Q4: 礼品兑换中的手机充值卡兑换，怎么样进行充值，充值之前会和我电话确认嘛？</h3>
      <p>不会，手机充值卡兑换，是直接充值到您填写的手机号上，充值之前不会和您电话确认，所以您在填写电话的时候，请确认电话是否正确。</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "integralissue",
  data() {
    return {};
  },
  methods: {
    back1() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.integralissue {
  background: white;
}
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.46rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.46rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 文字 */
.balan_condition {
  font-size: 0.14rem;
  color: #666;
  padding: 0 0.117rem 0.117rem;
  margin-top: 0.46rem;
}
.balan_condition h3 {
  line-height: 0.46875rem;
}
.balan_condition p {
  font-size: 0.14rem;
  color: #666;
  line-height: 0.234375rem;
}
</style>



