<template>
  <div class="recommend">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>推荐有奖</div>
    </header>

    <!-- 大图部分 -->
    <section class="recommend_banner">
      <img :src="img1" alt>
    </section>

    <!-- logo列表 -->
    <section class="logo_list">
      <div class="list_picture">
        <img :src="img2" alt>
        <p>邀请微信好友</p>
      </div>
      <div class="list_picture">
        <img :src="img3" alt>
        <p>邀请QQ好友</p>
      </div>
      <div class="list_picture">
        <img :src="img4" alt>
        <p>面对面邀请</p>
      </div>
    </section>

    <!-- content部分 -->
    <section class="recommend_con">
      <div class="content_left">
        <p>累计收益</p>
        <p>
          <span>0</span>元
        </p>
      </div>
      <div class="content_left content_right">
        <p>成功邀请</p>
        <p>
          <span>0</span>人
        </p>
      </div>
    </section>

    <!-- 收益 -->
    <p class="income_detail">-收益明细-</p>

    <!-- 邀请好友 -->
    <section class="income_tips">
      <img :src="img5" alt>
      <p>还不赶紧去邀请好友</p>
    </section>
  </div>
</template>

<script>
import img1 from "../../../../../assets/banner.png";
import img2 from "../../../../../assets/vx.png";
import img3 from "../../../../../assets/QQ.png";
import img4 from "../../../../../assets/二维码.png";
import img5 from "../../../../../assets/wallet.png";

export default {
  name: "recommend",
  data() {
    return {
      img1,
      img2,
      img3,
      img4,
      img5
    };
  },
  created() {},
  methods: {
    back1() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* banner部分 */
.recommend_banner {
  margin-top: 0.457rem;
}
.recommend_banner img {
  width: 100%;
  height: 2.34375rem;
}

/* logo列表 */
.logo_list {
  display: flex;
  padding: 0.234375rem 0;
  background-color: #fff;
}
.list_picture {
  flex: 1;
  text-align: center;
}
.list_picture img {
  width: 0.5859rem;
  height: 0.5859rem;
  margin-bottom: 0.05rem;
}
.list_picture p {
  font-size: 0.117rem;
  color: #333;
  line-height: 0.16rem;
}

/* content部分 */
.recommend_con {
  display: flex;
  margin-top: 0.234375rem;
  font-size: 0.117rem;
  color: #666;
}
/* 左 */
.content_left {
  flex: 1;
  text-align: center;
}
.content_left p {
  color: #666;
  line-height: 0.16rem;
  margin-bottom: 0.05rem;
}
.content_left span {
  font-size: 0.1875rem;
  color: #ff5633;
  font-weight: 700;
}

/* 右 */
.content_right {
  border-left: 0.0058rem solid #ddd;
}
.content_right span {
  font-size: 0.1875rem;
  color: #666;
  font-weight: 700;
}

/* 收益 */
.income_detail {
  font-size: 0.117rem;
  color: #666;
  line-height: 0.16rem;
}

.income_detail,
.income_tips {
  text-align: center;
  margin-top: 0.234375rem;
}

/* 邀请好友  */
.income_tips img {
  width: 0.30468rem;
  height: 0.375rem;
  margin-bottom: 0.05rem;
}
.income_tips p {
  font-size: 0.117rem;
  color: #999;
  line-height: 0.16rem;
}
</style>



