<template>
  <section class="tipContainer animated bounceIn" v-show="$store.state.flag!=''">
    <div class="tipIcon">
      <div class="icon1"></div>
      <div class="icon2"></div>
    </div>
    <p class="tipText">{{$store.state.erroMsg}}</p>
    <div class="confirm" @click="confirmThing()">确认</div>
  </section>
</template>
<script> 
export default {
  name: "warn",
  methods: {
    confirmThing() {
      this.$store.state.flag = false;
      if (this.$store.state.go!='') {
        this.$router.push(this.$store.state.go);
      }
    } 
  }
};
</script>
<style>
.tipContainer {
  position: absolute;
  top: 45%;
  left: 50%;
  width: 2.81rem;
  height: 1.7rem;
  margin-top: -1rem;
  margin-left: -1.405rem;
  background-color: white;
  padding-top: 0.15rem;
  border: 1px solid green;
  border-radius: 0.05rem;
}
.tipIcon {
  width: 0.63rem;
  /* height: 0.63rem; */
  /* background: blue; */
  margin: 0 auto;
  border: 0.03rem solid #f8cb86;
  border-radius: 50%;
  text-align: center;
}
.icon1 {
  width: 0.05rem;
  height: 0.35rem;
  background: #f8cb86;
  margin: 0.08rem auto 0;
}
.icon2 {
  width: 0.05rem;
  height: 0.05rem;
  background: #f8cb86;
  margin: 0.07rem auto;
}
.confirm {
  font-size: 0.2rem;
  color: #fff;
  font-weight: 700;
  margin-top: 0.23rem;
  background-color: #4cd964;
  width: 100%;
  text-align: center;
  line-height: 0.42rem;
  border: 1px;
  border-bottom-left-radius: 0.05rem;
  border-bottom-right-radius: 0.05rem;
}
.tipText {
  font-size: 0.17rem;
  color: #333;
  line-height: 0.19rem;
  text-align: center;
  margin-top: 0.18rem;
  padding: 0 0.14rem;
}
</style>




