<template>
  <div class="orderChoice animated fadeIn">
     <headZj></headZj>
      <ul class='order_ul'>
        <li class="order_list" v-for="(list,index) in lists" :key="index">
          <img :src="'//elm.cangdu.org/img/'+ list.restaurant_image_url" alt="">
          <section class="list_righ">
            <section>
              <header class="right_head">
                <section class="head_list">

                  <h4>
                    <span class="list_shop">{{list.restaurant_name}}</span>
                    <i class="el-icon-arrow-right"></i>
                  </h4>
                  <p class="list_time">{{list.formatted_created_at}}</p>
                </section>

                <p class="head_overtime">支付超时</p>
              </header>

              <section class="right_con">
                <p class="con_commodity">
                  <ul>
                    <li v-for='(site,index) in list.basket.group' :key='index'>

                      <p v-for='(sit,index) in site' :key='index'>
                        <span v-if='sit.quantity>0'>{{sit.name}}
                          <span class='orderNum'>X{{sit.quantity}}</span>
                        
                        </span>
                        
                        </p>
                    </li>
                  </ul>

                </p>
                <p class="con_dollar">￥{{list.total_amount}}</p>
              </section>
            </section>
            <div class="buy_again">
                <span>再来一单</span>
            </div>
          </section>
        </li>
      </ul>
  </div>
</template>
<script>
import headZj from '../../public/header/headZj'
export default {
  name: "orderChoice",
  data(){
      return{
          lists:[]
      }
  },
  components:{
    headZj
  },
  created(){
     this.$store.state.wanted='订单列表'
     this.orderList();
     this.$store.state.go = ''
  },
  methods:{
      orderList(){
        const url = "https://elm.cangdu.org/bos/v2/users/"+this.$store.state.userInfo.user_id+"/orders?limit=10&offset=0"
        this.$http({
          method:"get",
          url:url
        }).then((res) => {
          console.log(res.data)
          this.lists = res.data
        })
      },
  }
};
</script>
<style  scoped>
.animated {
  animation-duration: 1.5s;
}

.order_ul {
  padding-top: 0.457rem;
  padding-right:0.14rem;
}
.order_list {
  background-color: #fff;
  display: flex;
  margin-bottom: .117rem;
  padding: .14rem .14rem 0;

}
.order_list img {
  width: 0.46875rem;
  height: 0.46875rem;
  margin-right: .09375rem;
}

.right_head {
    border-bottom: 0.005rem solid #f5f5f5;
    padding-bottom: .07rem;
    display: flex;
    justify-content: space-between;
}


.head_list h4 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: .17578rem;
  color: #333;
  line-height: 0.234375rem;
  width: 2.109rem;
}
.list_shop {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 0.23rem;
}
.head_list i {
    fill: #ccc;
    margin-right: .046875rem;
    font-size: 0.09rem;
}

.list_time {
    font-size: .1289rem;
    color: #999;
    line-height: .1875rem;
    line-height: 0.18rem;
}

.head_overtime {
  font-size: .14rem;
  color: #333;
  float: left;
}

.right_con {
    display: flex;
    justify-content: space-between;
    line-height: 0.46875rem;
    border-bottom: 0.005rem solid #f5f5f5;
}

.con_commodity {
  font-size: .14rem;
  color: #666;
  width: 2.1rem;
}

.con_dollar {
  font-size: .14rem;
  color: #f60;
  font-weight: 700;
  margin-top:0.45rem;;
}

.buy_again {
  text-align: right;
    line-height: 0.375rem;
}
.buy_again span {
  font-size: .1289rem;
    color: #3190e8;
    border: .005rem solid #3190e8;
    padding: .0234375rem .046875rem;
    border-radius: .035156rem;
}
.orderNum{
  color:#f60;
}
</style>




