<template>
  <div class="order">
    <router-view></router-view>
    <div class="nav">
      <ul class="navUls">
        <li>
          <router-link to="/takeOut">
            <i class="fa fa-bicycle fa-2x"></i>
            <br>
            <span>外卖</span>
          </router-link>
        </li>
        <li>
          <router-link to="/searchPro">
            <i class="fa fa-compass fa-2x"></i>
            <br>
            <span>搜索</span>
          </router-link>
        </li>
        <li>
          <router-link to="/orderChoice">
            <i class="fa fa-reorder (alias) fa-2x"></i>
            <br>
            <span>订单</span>
          </router-link>
        </li>
        <li>
          <router-link to="/personal">
            <i class="fa fa-user-o fa-2x"></i>
            <br>
            <span>我的</span>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "order",
  data() {
    return {
      flag: true
    };
  },
  created() {
    console.log(this.$route.params);
  },
  methods: {
    changeLogo() {}
  }
};
</script>
<style scoped>
.nav {
  color:grey;
  width: 100%;
  height: 0.457rem;
  position: fixed;
  bottom: 0;
  border-top: 0.01rem solid lightgrey;
  background: white;
  z-index:99999;
}
.navUls li {
  width: 25%;
  /* background: red; */
  font-size: 0.1rem;
  float: left;
  text-align: center;
}
i {
  width: 0.1875rem;
  height: 0.1875rem;
  margin-top: 0.055rem;
  margin-bottom: 0.05rem;
}
a {
  width: 0.2rem;
  margin: 0 auto;
  color:blue;
}
span {
  font-size: 0.1rem;
  color: #666;
}
.router-link-exact-active{
    color:red;
}
</style>




