<template>
    <div class="purchase">
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>购买记录</div>
        </header>

        <div class="con_img">
            <img :src="imgs" alt="">
            <p>没有购买记录</p>
        </div>
    </div>
</template>

<script>
import imgs from '../../../../../assets/账单.png'

export default {
    name:"purchase",
    data() {
        return {
            imgs
        }
    },
    methods: {
        back1(){
            this.$router.go(-1)
        }
    },
}
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

.con_img {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.457rem;
}

.con_img img {
    width: 1.875rem;
    height: 1.171875rem;
    margin-top: 1.171875rem;
}
.con_img p {
    font-size: .14rem;
    color: #999;
    margin-top: .1875rem;
}
</style>



