<template>
  <div class="storeDetail">
    <nav class="back">
      <i class="fa fa-angle-left fa-2x" @click.stop="goLast();"></i>
    </nav>
    <div class="detailHead">
      <img :src="$store.state.shopBaseUrl + shopInfo.image_path" alt class="bImg">
      <div class="descrip">
        <router-link to="/shopInfor" class="desTop">
          <div class="logo">
            <img :src="$store.state.shopBaseUrl + shopInfo.image_path" alt>
          </div>
          <div class="logoRight">
            <h4>{{shopInfo.name}}</h4>
            <p class="p1">商家配送/分钟送达/{{tips}}</p>
            <p class="p2">公告：{{shopInfo.promotion_info}}</p>
          </div>
          <i class="fa fa-angle-right toRight fa-2x"></i>
        </router-link>
        <div class="bot" v-if="activities.length>0">
          <p class="incre" v-for="(cheap,index) in activities" :key="index">
            <span class="bs1">{{cheap.icon_name}}</span>
            <span class="bs2">{{cheap.description}}(APP专享)</span>
          </p>
          <p class="act" @click="shaSb=!shaSb">{{activities.length}}个活动</p>
          <i class="fa fa-angle-right toR"></i>
        </div>
      </div>
    </div>
    <div class="changeShow">
      <div class="changeLeft">
        <span class="chang1">
          <router-link :to="{name:'product'}">商家</router-link>
        </span>
      </div>
      <div class="changeRight">
        <span class="chang1">
          <router-link :to="{name:'evalute'}">评价</router-link>
        </span>
      </div>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
   <div v-if="shaSb" class="mengban">
      <div class="titledsDiv">
        <p>{{shopInfo.name}}</p>
      </div>
      <div class="youhuiDiv">
        <p>优惠信息</p>
      </div>
      <div class="manjianDiv">
        <p>
          <span class="sapnPsd">{{shadongxi[0].icon_name}}</span>
         <span class="sapnPse">{{shadongxi[0].description}}(APP专享)</span>
        </p>
      </div>
      <div class="gonggaoDiv">
        <p>商家公告</p>
      </div>
      <div class="guanglinDiv">
        <p>{{shopInfo.promotion_info}}</p>
      </div>
      <div  class="iDivs" @click="shaSb=!shaSb"><i class="el-icon-circle-close-outline"></i></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "storeDetail",
  data() {
    return {
      shopInfo: "",
      activities: "",
      tips: "",
      total: 0.0,
      flag: false,
      shaSb:false,
      shadongxi:""
    };
  },
  created() {
    this.$store.state.bigNum = [];
    this.$store.state.count = 0;
    this.$store.state.sum = 0;
    this.getShopDetail();
   this.hhhh();
  },
  methods: {
    hhhh(){
this.$http({
  method:"get",
  url:"https://elm.cangdu.org/shopping/restaurant/"+this.shopInfo.id
}).then((res) => {
    console.log(res);
    this.shadongxi = res.data.activities;
})
    },
    getShopDetail() {
           this.shopInfo = this.$store.state.shopInfo;
          console.log(this.shopInfo)

          this.activities = this.shopInfo.activities;
          console.log(this.shopInfo.activities);
          
          this.tips = this.shopInfo.piecewise_agent_fee.tips;
       
    },
    goLast() {
      this.$router.push({ name: "takeOut" });
    },
    goShopInfo() {
      if (this.activities.length > 0) {
        this.$router.push({ name: "shopInfor"});
      } else {
        this.goLast();
      }
    },
    goCheap() {
      this.flag = true;
    }
  }
};
</script>
<style scoped>
.cardFoodList {
  position: fixed;
  width: 100%;
  padding-bottom: 0.46875rem;
  z-index: 12;
  bottom: 0;
  left: 0;
  background-color: #fff;
}
.router-link-exact-active {
  color: #3190e8;
  border-bottom: 0.03rem solid #3190e8;
}
.changeShow span {
  line-height: 0.31rem;
  font-size: 0.16rem;
  color: #666;
  padding: 0.04688rem 0.02344rem;
  border-bottom: 0.01rem solid #fff;
}
.changeLeft {
  text-align: center;
  width: 1.875rem;
  float: left;
}
.changeRight {
  text-align: center;
  width: 1.875rem;
  float: left;
}
.changeShow {
  /* display: flex; */
  background-color: #fff;
  padding: 0.07rem 0 0.14rem;
  border-bottom: 1px solid #ebebeb;
  z-index: 10;
}
.toR {
  color: white;
  margin-top: -0.02rem;
}
.act {
  font-size: 0.1rem;
  color: #fff;
}
.bs2 {
  color: #fff;
}
.bs1 {
  background-color: rgb(240, 115, 115);
  border-color: rgb(240, 115, 115);
}
.incre {
  width: 78%;
}
.bot {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  margin-top: 0.11719rem;
  padding-right: 0.23438rem;
}
.toRight {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.3rem;
  z-index: 11;
}
.p1 {
  color: #fff;
  width: 2.695rem;
  margin-top: 0.1rem;
}
.p2 {
  color: #fff;
  margin-bottom: 0.07rem;
  margin-top: 0.1rem;
}
.logoRight p {
  font-size: 0.12rem;
}
.logoRight h4 {
  font-size: 0.16rem;
  color: #fff;
  font-weight: 700;

  margin-bottom: 0.07rem;
}
.logoRight {
  flex: 1;
}
.logo img {
  width: 0.67969rem;
  height: 0.67969rem;
  display: block;
  border-radius: 0.015rem;
}
.logo {
  margin-right: 0.07rem;
}
.desTop {
  display: flex;
  color: white;
}
.descrip {
  position: relative;
  z-index: 10;
  background-color: rgba(119, 103, 137, 0.43);
  padding: 0.09375rem 0 0.09375rem 0.09375rem;
  overflow: hidden;
}
.storeDetail {
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  position: absolute;
  right: 0;
  left: 0;
  height: 100%;
}
nav {
  padding: 0.0488rem 0 0 0.0488rem;
  height: 0.42rem;
  color: white;
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
}
.detailHead {
  z-index: 10;
  overflow: hidden;
  position: relative;
  width: 3.75rem;
}
.bImg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  -webkit-filter: blur(10px);
  filter: blur(10px);
}
.mengban {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
  z-index: 99999999999999;
  color: white;
}
.titledsDiv {
  width: 100%;
  text-align: center;
  margin-top: 0.2rem;
  font-size: 0.18rem;
}
.youhuiDiv {
  width: 100%;
  text-align: center;
  margin-top: 0.5rem;
  font-size: 0.12rem;
}
.youhuiDiv > p {
  text-align: center;
  margin: 0 auto;
  width: 1rem;
  height: 0.2rem;
  padding: 0.02;
  line-height: 0.2rem;
  border: 0.01rem solid gray;
}
.gonggaoDiv {
  width: 100%;
  text-align: center;
  margin-top: 0.5rem;
  font-size: 0.12rem;
}
.gonggaoDiv > p {
  text-align: center;
  margin: 0 auto;
  width: 1rem;
  height: 0.2rem;
  padding: 0.02;
  line-height: 0.2rem;
  border: 0.01rem solid gray;
}
.manjianDiv{
   width: 100%;
  margin-left: 0.3rem;
  margin-top: 0.2rem;
  font-size: 0.12rem;
}
.guanglinDiv{
    width: 100%;
  margin-left: 0.3rem;
  margin-top: 0.2rem;
  font-size: 0.12rem;
}
.iDivs{
  width: 100%;
  position: absolute;
  bottom: 0.2rem;
  left: 0;
  text-align: center;
}
.el-icon-circle-close-outline{
  /* border: 0.01rem solid gray; */
  margin: 0 auto;
  font-size: 0.5rem;
  color: gray
}
.sapnPsd{
  /* color: red; */
  padding: 0.02rem ;
  background-color: red;
}
</style>



