<template>
  <div class="home">
    <div class="head">
      <span class="span1">ele.me</span>
      <span class="span2">
        
        <router-link to="/login" v-if="!$store.state.userInfo.id">登陆|注册</router-link>
        <router-link :to="{name:'personal'}" v-else><i class="fa fa-user-o "></i></router-link>
      </span>
    </div>
    <div class="content">
      <div class="locate">
        <span class="span1">当前定位城市:</span>
        <span class="span2 deepSpan">定位不准时，请在城市列表中选择</span>
      </div>
      <div class="location" @click="cts(locatCity.id,locatCity.name)">
            <span class="span1 guessCity">{{locatCity.name}}</span>
        <span class="span2">
          <i class="el-icon-arrow-right"></i>
        </span>
      </div>
      <div class="space"></div>
      <div class="hotCity">
        <div class="hotCityTitle">
          <span class="span1">热门城市</span>
        </div>
        <div class="hotCityName">
          <ul>
            <li v-for="(city,index) in hotCitys" :key="index"  @click="cts(city.id,city.name)">{{city.name}}</li>
          </ul>
        </div>
          <div class="space"></div>
        <div class="cityChoice">
          <ul v-for="(city,index) in sequence" :key="index" class="ulo">
            <li class="word" v-if="city==='A'">{{city}}<span>(按字母排序)</span></li>
            <li class="word" v-else>{{city}}</li>
            <li>
              <ul class="uls">
                <li v-for="(citys,index) in groupCitys[city]" :key="index" @click="cts(citys.id,citys.name)">{{citys.name}}</li>
              </ul>
            </li>
            <li class="space"></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "home",
  data() {
    return {
      idYa:"",
      locatCity: "",
      hotCitys: "",
      groupCitys: "",
      sequence: "",
    };
  },
  created() {
    this.guessCity();
    this.hotCity();
    this.groupCity();
  },
  methods: {
    cts(idA,nameA){
        localStorage.setItem('cityId',idA)
        localStorage.setItem('cityName',nameA)
        this.$router.push({
          name: 'search',
        })
    },
    guessCity() {
      const url = "https://elm.cangdu.org/v1/cities?type=guess";
      this.$http({
        method: "get",
        url: url
      })
        .then(result => {
          this.locatCity = result.data;
          // 定位信息
          this.$store.state.guessCityInfo = result.data;
          // 本地存储定位信息
          localStorage.setItem('latitude',this.$store.state.guessCityInfo.latitude);
         localStorage.setItem('longitude',this.$store.state.guessCityInfo.longitude);
    
        })
        .catch(err => {});
    },
    hotCity() {
      const url = "https://elm.cangdu.org/v1/cities?type=hot";
      this.$http({
        method: "get",
        url: url
      })
        .then(result => {
          this.hotCitys = result.data;
        })
        .catch(err => {});
    },
    groupCity() {
      const url = "https://elm.cangdu.org/v1/cities?type=group";
      this.$http({
        method: "get",
        url: url
      })
        .then(result => {
          this.compare(result.data);
          this.groupCitys = result.data;
       
          
        })
        .catch(err => {});
    },
    compare(obj) {
      this.sequence = Object.keys(obj).sort();
       
    }
  }
};
</script>
<style scoped>
.head {
  width: 100%;
  position: fixed;
  top: 0;
  height: 0.457rem;
  background: #3190e8;
  line-height: 0.457rem;
  font-size: 0.16rem;
  color: white;
}
.head a{
  color:white;
}
.content {
  width: 100%;
  overflow: hidden;
  padding-top: 0.55rem;
}
.locate {
  width: 100%;
  line-height: 0.33rem;
  font-size: 0.1rem;
  background: white;
}
.deepSpan{
   font-weight: bold;
  color:#9f9f9f;
  font-size:0.14rem;
}
.span1 {
  margin-left: 0.1rem;
}
.span2 {
  float: right;
  margin-right: 0.15rem;
}
.location {
  width: 100%;
  height: 0.4219rem;
  line-height: 0.4219rem;
  border-top: 0.01rem solid #e4e4e4;
  background: white;
}
.hotCity {
  width: 100%;

}
.hotCityTitle {
  line-height: 0.36rem;
  border-bottom: 0.02rem solid #e4e4e4;
  font-size: 0.055rem;
}
.hotCityName {
  height: 0.8rem;
}
.hotCityName li {
  float: left;
  text-align: center;
  color: #3190e8;
  line-height: 0.41rem;
  width: 25%;
  height: 50%;
  box-sizing: border-box;
  border-bottom: 0.001rem solid #e4e4e4;
  border-right: 0.001rem solid #e4e4e4;
}
.cityChoice {
  font-size:0.15rem;
  color:#666;
}
.cityChoice ul {
  width: 100%;

  overflow: hidden;
}
.cityChoice ul li {
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.word {
  width: 100%;
  border-bottom: 0.01rem solid #e4e4e4;
  height: 0.36rem;
  line-height: 0.36rem;
  padding-left: 0.1rem;
}
.word span{
  font-size:0.12rem;
  color:#999;
  margin-left:0.02rem;
}
.ulo {
  border: 0.01rem solid #e4e4e4;
}

.uls li {
  width: 25%;
  box-sizing: border-box;
  height: 50%;
  border-bottom: 0.001rem solid #e4e4e4;
  border-right: 0.001rem solid #e4e4e4;
  height: 0.41rem;
  line-height: 0.41rem;
  text-align: center;
}
.guessCity{
  color:#3190e8;
}
.space{
  width:100%;
  height: 0.1rem;
  background: #e4e4e4;
}

</style>




