<template>
  <div class="resetCode">
    <warn></warn>
      <headZj></headZj>
    <div class="inputSquare">
      <div class="count">
        <input type="text" placeholder="账号" v-model="username">
      </div>
      <div class="oldPwd">
        <input type="text" placeholder="旧密码" v-model="oldPwd">
      </div>
      <div class="newPwd">
        <input type="text" placeholder="请输入新密码" v-model="newPwd">
      </div>
      <div class="confirmPwd">
        <input type="text" placeholder="请确认密码" v-model="confirm">
      </div>
    </div>
    <div class="codeCheck">
      <input type="text" v-model="codeNumber" class="noClick" placeholder="验证码">

      <div class="img">
        <img :src="code&&code" alt>
        <div @click="getCode()" class="next">
          <p>看不清</p>
          <p class="p2">换一张</p>
        </div>
      </div>
    </div>
    <div class="confirmChange" @click="changePwd()">
      <span>确认修改</span>
    </div>
  </div>
</template>
<script>
import warn from '../public/warn'
import headZj from '../public/header/headZj'
export default {
  name: "resetCode",
  data() {
    return {
      code: "",
      codeNumber: "",
      username: "",
      oldPwd: "",
      newPwd: "",
      confirm: ""
    };
  },
  components:{
    warn,
    headZj
  },
  created() {
    this.getCode();
    this.$store.state.wanted='重置密码'
  },
  
  methods: {
    getCode() {
      const url = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        method: "post",
        url: url,
        withCredentials: true
      }).then(res => {
        if (res.status == 200) {
          this.code = res.data.code;
        }
      });
    },
    changePwd() {
      const url = "https://elm.cangdu.org/v2/changepassword";
      this.$http({
        method: "post",
        url: url,
        data:{
          username: this.username,
          oldpassWord: this.oldPwd,
          newpassword: this.newPwd,
          confirmpassword: this.confirm,
          captcha_code: this.codeNumber
        },
        withCredentials: true
      }).then(res => {
       if(res.data.success){
         this.$store.state.erroMsg = res.data.success
          this.$store.state.flag=true;
       }else{
          this.$store.state.erroMsg = res.data.message
          console.log(res.data.message)
          this.$store.state.flag=true;
       }
       
      });
    },
  }
};
</script>
<style scoped>
.recode {
  font-size: 0.2rem;
  font-weight: bold;
  margin-left: -0.3rem;
}
.inputSquare {
  padding-top:0.55rem;
  
}
.inputSquare div {
  border-top: 0.01rem solid #eee;
  padding: 0.07rem 0.1875rem;
}
input {
  outline: none;
  height: 0.376rem;
  line-height: 0.376rem;
  font-size: 0.17rem;
}
.codeCheck {
  padding: 0.07rem 0.1875rem;
  border-top: 0.01rem solid #eee;
  border-bottom: 0.01rem solid #eee;
}
.img {
  float: right;
  border-top: none;
}
img {
  width: 0.8rem;
  height: 0.376rem;
}
.next {
  display: inline-block;
  font-size: 0.12rem;
}
.p2 {
  color: blue;
  margin-top: 0.07rem;
}
.confirmChange {
  width: 90%;
  margin: 0 auto;
  height: 0.4444rem;
  line-height: 0.4444rem;
  text-align: center;
  margin-top: 0.24rem;
  color: #fff;
  background-color: #4cd964;
  border: 1px;
  border-radius: 0.05rem;
}
</style>




