<template>
  <div class="elmvip">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>会员中心</div>
    </header>

    <!-- content -->
    <section class="elmvip_section">
      <!-- title -->
      <p class="buy_for">
        为账户
        <span>{{this.$store.state.userInfo.username}}</span>
        购买会员
      </p>

      <!-- 会员特权 -->
      <section class="vip_privilege">
        <router-link to="/privilege" class="header_style">
          <span class="header_left">会员特权</span>
          <section class="header_right">
            <span>会员说明</span>
            <i class="el-icon-arrow-right"></i>
          </section>
        </router-link>

        <!-- content logo -->
        <section class="vie_detail">
          <img :src="img10" alt class="vip_detail_left">
          <div class="vip_detail_right">
            <h4>减免配送费</h4>
            <p>每月减免30单，每日可减免3单，每单最高减4元</p>
            <p>蜂鸟专送专享</p>
          </div>
        </section>

        <section class="vie_detail">
          <img :src="img11" alt class="vip_detail_left">
          <div class="vip_detail_right">
            <h4>减免配送费</h4>
            <p>每月减免30单，每日可减免3单，每单最高减4元</p>
            <p>蜂鸟专送专享</p>
          </div>
        </section>
      </section>

      <!-- 开通会员 -->
      <section class="apply_vip">
        <header class="header_style">
          <span class="header_left">开通会员</span>
        </header>
        <section class="apply_vip_buy">
          <div class="apply_vip_buy_left">
            <span>1个月</span>
            <span>￥20</span>
          </div>
          <!-- 在线支付 -->
          <router-link to="/payment">
            <div class="apply_vip_buy_right">购买</div>
          </router-link>
        </section>
      </section>

      <!-- 兑换会员 -->
      <router-link to="/vipExchange" class="header_style common_style">
        <span class="header_left">兑换会员</span>
        <section class="header_right">
          <span>使用卡号卡密</span>
          <i class="el-icon-arrow-right"></i>
        </section>
      </router-link>

      <!-- 购买记录 -->
      <router-link to="/purchase" class="header_style common_style">
        <span class="header_left">购买记录</span>
        <section class="header_right">
          <span>开发票</span>
          <i class="el-icon-arrow-right"></i>
        </section>
      </router-link>
    </section>
  </div>
</template>

<script>
import img10 from "../../../../assets/10.png";
import img11 from "../../../../assets/11.png";

export default {
  name: "elmvip",
  data() {
    return {
      img10,
      img11
    };
  },
  created() {},
  methods: {
    back1() {
      this.$router.push({name:"personal"});
    },
  }
};
</script>


<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* content */
.elmvip_section {
  margin-top: 0.457rem;
}
/* title */
.buy_for {
  font-size: 0.14rem;
  color: #666;
  line-height: 0.46875rem;
  padding-left: 0.164rem;
}
.buy_for span {
  font-weight: 700;
  font-family: Helvetica Neue,Tahoma,Arial;
}

/* 会员特权 */
.vip_privilege {
  background-color: #fff;
  padding-left: 0.164rem;
  margin-bottom: 0.117rem;
}

.header_style {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  line-height: 0.46875rem;
  border-bottom: 0.01rem solid #f5f5f5;
  padding-right: 0.164rem;
}

.header_left {
  font-size: 0.164rem;
  color: #333;
}

.header_right {
  display: flex;
  align-items: center;
}
.header_right span {
  font-size: 0.14rem;
  color: #999;
  margin-right: 0.046875rem;
}
.header_right i {
  font-size: 0.1172rem;
}

/* content logo */
.vie_detail {
  display: flex;
  border-bottom: 0.01rem solid #f5f5f5;
  padding: 0.1875rem 0;
}
.vip_detail_left {
  margin-right: 0.14rem;
  width: 0.375rem;
  height: 0.421875rem;
}

.vip_detail_right h4 {
  font-size: 0.164rem;
  color: #333;
  font-weight: 400;
  line-height: 0.21rem;
}
.vip_detail_right p {
  font-size: 0.117rem;
  color: #999;
  line-height: 0.16rem;
}

/* 开通会员 */
.apply_vip {
  background-color: #fff;
  padding-left: 0.164rem;
  margin-bottom: 0.117rem;
}

.apply_vip_buy {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 0.164rem;
  font-size: 0.164rem;
  line-height: 0.609375rem;
}

.apply_vip_buy_left span {
  font-family: Helvetica Neue, Tahoma, Arial;
}
.apply_vip_buy_left span:nth-of-type(2) {
  font-weight: 700;
  color: #f60;
}

.apply_vip_buy_right {
  border: 0.005859rem solid #f60;
  border-radius: 0.046875rem;
  line-height: 0.28125rem;
  height: 0.28125rem;
  width: 0.609375rem;
  text-align: center;
  font-size: 0.14rem;
  color: #f60;
}

/* 兑换会员 */
.common_style {
  padding: 0 0.164rem;
  margin-bottom: 0.117rem;
}
</style>



