<template>
  <div class="personal animated fadeIn">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>我的</div>
    </header>

    <!-- 内容部分 -->
    <section class="per_content">
      <!-- 账号部分 -->
      <section class="content_num">
        <router-link to="/account" v-if="username">
          <img :src="img1" class="num_imges">
          <div class="user_info">
            <p>{{username}}</p>
            <p>
              <span class="el-icon-mobile-phone"></span>
              <span class="phone">暂无绑定手机号</span>
            </p>
          </div>
          <span class="el-icon-arrow-right"></span>
        </router-link>

        <router-link to="/login" v-else>
          <img :src="img2" class="num_imges">
          <div class="user_info">
            <p>登录/注册</p>
            <p>
              <span class="el-icon-mobile-phone"></span>
              <span class="phone">暂无绑定手机号</span>
            </p>
          </div>
          <span class="el-icon-arrow-right"></span>
        </router-link>
      </section>

      <!-- 余额部分 -->
      <section class="info_data">
        <ul class="data_clear">
          <router-link to="/balance" v-if="username">
            <li class="clear_li">
              <span class="li_top">
                <b>{{this.$store.state.userInfo.balance}}.00</b>
                元
              </span>
              <span class="li_buttom">我的余额</span>
            </li>
          </router-link>
          <router-link to="/balance" v-else>
            <li class="clear_li">
              <span class="li_top">
                <b>0.00</b>
                元
              </span>
              <span class="li_buttom">我的余额</span>
            </li>
          </router-link>

          <router-link to="/discounts" v-if="username">
            <li class="clear_li">
              <span class="li_top">
                <b class="li_top_b">{{indexs}}</b>
                个
              </span>
              <span class="li_buttom">我的优惠</span>
            </li>
          </router-link>
          <router-link to="/discounts" v-else>
            <li class="clear_li">
              <span class="li_top">
                <b class="li_top_b">0</b>
                个
              </span>
              <span class="li_buttom">我的优惠</span>
            </li>
          </router-link>

          <router-link to="/integral">
            <li class="clear_li">
              <span class="li_top">
                <b class="li_top_color">0</b>
                分
              </span>
              <span class="li_buttom">我的积分</span>
            </li>
          </router-link>
        </ul>
      </section>

      <!-- 订单/商城 -->
      <section class="outline">
        <!-- 订单 -->
        <router-link to="/orderChoice">
          <li class="myorder">
            <i class="el-icon-tickets"></i>
            <div class="myorder_div">
              <span>我的订单</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>
        <!-- 商城 -->
        <router-link to="/malfunction">
          <li class="myorder">
            <i class="el-icon-goods"></i>
            <div class="myorder_div">
              <span>积分商城</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>
        <!-- 饿了么会员 -->
        <router-link to="/elmvip">
          <li class="myorder">
            <i class="el-icon-upload"></i>
            <div class="myorder_div">
              <span>饿了么会员卡</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>
      </section>

      <!-- 服务中心部分 -->
      <section class="outline">
        <!-- 服务中心 -->
        <router-link to="/serve">
          <li class="myorder">
            <i class="el-icon-date"></i>
            <div class="myorder_div">
              <span>服务中心</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>
        <router-link to="/download">
          <li class="myorder">
            <i class="el-icon-setting"></i>
            <div class="myorder_div">
              <span>下载饿了么APP</span>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>
      </section>
    </section>
  </div>
</template>

<script>
import img1 from "../../../assets/picture.png";
import img2 from "../../../assets/1.jpg";

export default {
  name: "personal",
  data() {
    return {
      img1,
      img2,
      username: "",
      lists: "",
      indexs: ""
    };
  },
  created() {
    this.username = this.$store.state.userInfo.username;
    // console.log(this.$store.state.userInfo.username);
    this.hongbao();
  },
  methods: {
    back1() {
      this.$router.push({name:"takeOut"});
    },
    hongbao() {
      const url =
        "https://elm.cangdu.org/promotion/v2/users/" +
        this.$store.state.userInfo.id +
        "/hongbaos?limit=20&offset=0";
      this.$http({
        method: "get",
        url: url
      }).then(res => {
        // console.log(res.data)
        this.lists = res.data;
        this.indexs = res.data.length;
        this.$store.state.hotbagList = this.lists;
      });
    }
  }
};
</script>

<style scoped>
.personal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}
.animated {
  animation-duration: 1s;
}
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 中间内容部分 */
.per_content {
  margin-top: 0.457rem;
}

/* 账号部分 */
.content_num {
  display: flex;
  align-items: center;
  background: #3190e8;
  padding: 0.156rem 0.14rem;
  position: relative;
}
/* 图片头像 */
.num_imges {
  width: 0.5859rem;
  height: 0.5859rem;
  border-radius: 50%;
  vertical-align: -90%;
}
.user_info {
  margin-left: 0.1125rem;
  flex-grow: 1;
  display: inline-block;
}
.user_info p {
  font-weight: 700;
  font-size: 0.1875rem;
  line-height: 0.23rem;
  color: #fff;
}
/* 手机图标 */
.el-icon-mobile-phone {
  width: 0.117rem;
  height: 0.1757rem;
  vertical-align: -10%;
  line-height: 0.1757rem;
}
.phone {
  font-size: 0.1343rem;
  color: #fff;
}
/* 右侧>图标 */
.content_num .el-icon-arrow-right {
  width: 0.109rem;
  height: 0.2296rem;
  color: white;
  position: absolute;
  top: 40%;
  right: 0;
  margin-right: 0.15rem;
}

/* 余额部分 */
.info_data {
  width: 100%;
  background: #fff;
  box-sizing: border-box;
}
.data_clear {
  overflow: hidden;
}
.clear_li {
  float: left;
  width: 33%;
  border-right: 1px solid #f1f1f1;
}
.clear_li span {
  display: block;
  width: 100%;
  text-align: center;
}
.li_top {
  font-size: 0.1289rem;
  color: #333;
  padding: 0.2rem 0 0.13rem;
}
.li_top b {
  display: inline-block;
  font-size: 0.281rem;
  color: #f90;
  font-weight: 700;
  line-height: 0.234375rem;
}
.li_buttom {
  font-size: 0.13433rem;
  color: #666;
  font-weight: 400;
  padding-bottom: 0.1062rem;
}

.li_top .li_top_b {
  color: #ff5f3e;
}

.li_top .li_top_color {
  color: #6ac20b;
}

/* 订单/商城列表 */
.outline {
  margin-top: 0.1rem;
  background: #fff;
}

/* 我的订单 */
.myorder {
  padding-left: 0.375rem;
  display: flex;
  align-items: center;
}
.myorder i {
  width: 0.164rem;
  height: 0.164rem;
  margin-left: -0.2031rem;
  margin-right: 0.0625rem;
  display: flex;
}

.myorder_div {
  width: 100%;
  border-bottom: 1px solid #f1f1f1;
  padding: 0.12rem 0.06rem 0.12rem 0;
  font-size: 0.164rem;
  color: #333;
  display: flex;
  justify-content: space-between;
}

.myorder_div .el-icon-arrow-right {
  font-size: 0.14rem;
  color: #333;
}

/* 积分商城 */
.el-icon-goods {
  color: rgb(252, 123, 83);
}

/* 饿了么会员 */
.el-icon-upload {
  color: rgb(255, 198, 54);
}

/* 服务中心 */
.el-icon-date {
  color: rgb(74, 165, 240);
}

/* 下载饿了么 */
.el-icon-setting {
  color: rgb(60, 171, 255);
}
</style>



