<template>
    <div class="searchAddress">
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>搜索地址</div>
        </header>


        <!-- 搜索框 -->
        <section class="search_con">
            <div class="add_detail">
                <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="keyword">
                <button @click="addBtn()">确认</button>

                <router-view></router-view>
            </div>
            <div class="ask_for">
                为了满足商家的送餐要求，建议您从列表中选择地址
            </div>
            <div class="point" :class="keyword.length>=1!=''&&'hide'">
                <p>找不到地址？</p>
                <p>请尝试输入小区、写字楼或学校名</p>
                <p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
        </section>


        <section class="lists_container">
            <ul>
                <li v-for="(num,index) in nums" :key="index" @click="liBtn(num)">
                    <p>{{num.name}}</p>
                    <p>{{num.address}}</p>
                </li>
            </ul>
        </section>

    </div>
</template>

<script>
export default {
    name:"searchAddress",
    data() {
        return {
            keyword:"",
            nums:"",
            bl:0,
        }
    },
    created() {
            if(this.$route.params.id){
                this.bl = this.$route.params.id;
        }

        console.log(this.bl)
    
     
    },
    methods: {
        back1(){
            this.$router.go(-1)
        },
        addBtn(){
            const url = "https://elm.cangdu.org/v1/pois?city_id="+ this.$store.state.guessCityInfo.id +"&keyword="+this.keyword+""
            this.$http({
                method:"get",
                url:url
            }).then((res) => {
                console.log(res.data)
                this.nums = res.data
            })
        },
        liBtn(num){
            if(this.bl ==1){
                this.$router.go(-1);
                
                this.$store.state.info = num;
              
            }else{
                console.log(num.name)
            this.$router.push({name:"increase",params:{num}})
            }
          
        }
    },
}
</script>


<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}


.search_con {
    margin-top: 0.457rem;
}
.add_detail {
    display: flex;
    justify-content: space-between;
    background: #fff;
    padding: .117rem;
}
.add_detail input {
    display: block;
    width: 2.5582rem;
    padding: .09375rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: .14rem;
}
.add_detail button {
    display: block;
    width: 0.7175rem;
    height: 0.3975rem;
    background: #3199e8;
    font-size: .164rem;
    color: #fff;
    border-radius: 5px;
}

.ask_for {
    background: #fff6e4;
    font-size: .1453rem;
    color: #ff883f;
    text-align: center;
    padding: .046875rem 0;
    line-height: 0.19005rem;
}

.point {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
}
.point p {
    width: 100%;
    text-align: center;
    font-size: .164rem;
    color: #969696;
    margin-bottom: .09375rem;
    line-height: 0.21rem;
}
.hide {
    display: none;
}


.lists_container {
    background: #f2f2f2;
}

.lists_container li {
    border-bottom: 1px solid #ccc;
    padding: .09375rem;
}

.lists_container li:last-child {
    border-bottom: 0;
}

.lists_container li p:first-child {
    margin-bottom: .046875rem;
}
.lists_container li p {
    font-size: .15234rem;
    color: #969696;
    line-height: 0.2rem;
}
</style>



