<template>
  <div class="download">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>下载</div>
    </header>

    <!-- 内容 -->
    <section class="download_container">
      <img :src="img1" class="container_logo">
      <p>下载饿了么APP</p>
      <div class="determine" @click="download1()">下载</div>
    </section>
  </div>
</template>

<script>
import img1 from "../../../../assets/logo.jpg";
import warn from "../../../public/warn";
export default {
  name: "download",
  data() {
    return {
      img1
    };
  },
  created() {},
  components: {
    warn
  },
  methods: {
    back1() {
      this.$router.go(-1);
    },
    download1() {
      this.$store.state.erroMsg = "IOS用户请前往AppStore下载";
      this.$store.state.flag = true;
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 内容 */
.download_container {
  margin-top: 0.457rem;
  text-align: center;
}
.container_logo {
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 0.234375rem;
  margin-top: 0.234375rem;
  margin-bottom: 0.04rem;
}
.download_container p {
  font-size: 0.1875rem;
  color: #666;
  margin-bottom: 0.234375rem;
  line-height: 0.25rem;
}
.determine {
  background-color: #4cd964;
  font-size: 0.164rem;
  color: #fff;
  text-align: center;
  margin: 0 0.164rem;
  line-height: 0.421875rem;
  border-radius: 0.046875rem;
  margin-top: 0.117rem;
}
</style>



