<template>
    <div class="increase animated fadeInRight">
        <warn></warn>
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>新增地址</div>
        </header>

        <section class="adddetail">
            <form action="">
                <section class="padding_block">
                    <div class="input_new">
                        <input type="text" placeholder="请填写你的姓名" v-model="name">
                    </div>

                    <router-link to="/searchAddress" class="add-detail">
                        <div class="input_new">
                            <input type="text" placeholder="小区/写字楼/学校等" v-model="address" readonly v-if="this.$route.params.num">
                            <input type="text" placeholder="小区/写字楼/学校等" v-model="address" readonly v-else>
                        </div>
                    </router-link>

                    <div class="input_new">
                        <input type="text" placeholder="请填写详细送餐地址" v-model="address_detail">
                        <p :class="address_detail.length>=1&&address_detail.length<=2!=''&&'conceal'">送餐地址太短了,不能辨别</p>
                    </div>

                    <div class="input_new">
                        <input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone" minlength="11">
                        <p :class="phone.length>=1&&phone.length<=10!=''&&'conceal'">请输入正确的手机号</p>
                    </div>

                    <div class="input_new">
                        <input type="text" placeholder="备用联系电话（选填）" v-model="phone_bk" minlength="11">
                        <p :class="phone_bk.length>=1&&phone_bk.length<=10!=''&&'conceal'">请输入正确的手机号</p>
                    </div>
                </section>


                <section class="addbutton">
                    <div @click="btn()" :class="name.length>0&&address_detail.length>0!=''&&'adddiv'">新增地址</div>
                </section>
            </form>
        </section>
    </div>
</template>

<script>
import warn from "../../../../public/warn";

export default {
    name:"increase",
    data() {
        return {
            name:"",
            address:"",
            address_detail:"", 
            phone:"",
            phone_bk:"",
            latitude: this.$store.state.guessCityInfo.latitude,
            longitude: this.$store.state.guessCityInfo.longitude,
        }
    },
    created() {
        if(this.$route.params.num) {
            this.address = this.$route.params.num.name
        }
    },
    components:{
        warn
    },
    methods: {
        back1(){
            this.$router.push({name:"addressed"})
        },
        btn(){
            const url = "https://elm.cangdu.org/v1/users/"+ this.$store.state.userInfo.id+"/addresses"
            this.$http({
                method:"post",
                url:url,
                data:{
                    user_id: this.$store.state.userInfo.username,
                    address_detail: this.address_detail,
                    geohash: this.latitude+","+this.longitude,
                    name: this.name,
                    phone: this.phone,
                    tag: this.tag,
                    sex: "0",
                    phone_bk: this.phone_bk,
                    address: this.address,
                    tag_type: 2,
                    tag:"家"
                }
            }).then((res) => {
                console.log(res.data,111111)
                this.$store.state.erroMsg = res.data.message;
                this.$store.state.flag = true;
                if (!this.$store.state.erroMsg) {
                    this.$router.push({name:"addressed"})
                }
            })
        }
    },
}
</script>


<style scoped>
.increase {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  background: white;
}
.animated {
  animation-duration: 0.5s;
}

/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}



.adddetail {
    margin-top: .55075rem;
}
.padding_block {
    background: #fff;
    padding-top: .09375rem;
}
.input_new {
    padding-bottom: .09375rem;
}

.input_new input {
    display: block;
    width: 3.354rem;
    height: 0.191rem;
    font-size: .14rem;
    margin: 0 auto;
    padding: .07rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 3px;
}

.input_new p {
    font-size: .09375rem;
    color: #ea3106;
    padding-left: .117rem;
    margin-top: .046875rem;
    display: none
}

.input_new .conceal {
    display: block;
}


.add-detail {
    display: block;
}



.addbutton {
    margin: .14rem auto;
    width: 3.515625rem;
    background: #4cd964;
    border-radius: 3px;
}
.addbutton div {
    width: 100%;
    font-size: .14rem;
    color: #fff;
    line-height: 0.375rem;
    text-align: center;
    background: none;
    font-weight: 700;
    opacity: .6;
}

.addbutton .adddiv {
    transition: all .4s;
    opacity: 1;
}
</style>



