<template>
    <div class="historyred">
        <!-- 头部 -->
        <header class="head">
            <div class="el-icon-arrow-left" @click="back1()"></div>
            <div>历史红包</div>
        </header>


        <!-- 过期红包 -->
        <section class="history_hongbao">
            <ul class="hongbao_list_ul">
                <li class="hongbao_list_li" v-for="(list,index) in histList" :key="index">
                    <section class="list_item">
                        <div class="list_item_left">
                            <span>￥</span>
                            <span>{{list.amount}}</span>
                            <span>.</span>
                            <span>0</span>
                            <p>{{list.description_map.sum_condition}}</p>
                        </div>
                        <div class="list_item_right">
                            <h4>{{list.name}}</h4>
                            <p>{{list.description_map.validity_periods}}</p>
                            <p>{{list.description_map.phone}}</p>
                        </div>
                    </section>

                    <footer class="list_item_footer">
                        <p></p>
                    </footer>
                    <!-- <img :src="imageUrl" class="expired"> -->
                    <div class="expired">
                        <p>已过期</p>
                    </div>
                </li> 
            </ul>
        </section>
    </div>
</template>
<script>
import imageUrl from '../../../../../assets/timg.jpg'

export default {
    name:"historyred",
    data() {
        return {
            imageUrl,
            histList:""
        }
    },
    created() {
        this.expireds()
    },
    methods: {
        back1(){
            this.$router.go(-1);
        },
        expireds(){
            const url = "https://elm.cangdu.org/promotion/v2/users/"+this.$store.state.userInfo.id+"/expired_hongbaos?limit=10&offset=0";
            this.$http({
                method:"get",
                url:url
            }).then((res) => {
                console.log(res.data)
                this.histList = res.data
            })
        }
    },
}
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 过期红包 */
.history_hongbao {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 0.457rem;
}
.hongbao_list_ul {
    transform: translate(0px, 0px) translateZ(0px);
    padding: 0.234375rem .117rem;
}
.hongbao_list_li {
    background: #fff url("../../../../../assets/border.png") repeat-x;
    background-size: .117rem .046875rem;
    margin-bottom: .117rem;
    border-radius: .05859rem;
    position: relative;
}

.list_item {
    display: flex;
    padding: 0.234375rem .117rem .1875rem;
}

/* 内容左 */
.list_item_left {
    font-size: 0;
    border-right: .01rem dotted #ccc;
    flex: 1;
}
.list_item_left span:first-of-type {
    font-size: .17578rem;
    color: #ccc;
    font-weight: 700;
}
.list_item_left span:nth-of-type(2) {
    font-size: .46875rem;
    color: #ccc;
    line-height: 0.57rem;
}
.list_item_left span:nth-of-type(3),.list_item_left span:nth-of-type(4) {
    font-size: .234375rem;
    color: #ccc;
    font-weight: 700;
}
.list_item_left p {
    font-size: .09375rem;
    color: #999;
    line-height: 0.16rem;
}

/* 内容右 */
.list_item_right {
    flex: 2;
    margin-left: 0.35156rem;
}
.list_item_right h4 {
    font-size: .164rem;
    color: #666;
    margin-left: -.164rem;
    line-height: 0.21rem;
}
.list_item_right p {
    margin-left: -.164rem;
    font-size: .09375rem;
    color: #999;
    line-height: 0.16rem;
}


.list_item_footer {
    background-color: #f9f9f9;
    padding: .09375rem;
    border-bottom-left-radius: .05859rem;
    border-bottom-right-radius: .05859rem;
}
.list_item_footer p {
    font-size: .09375rem;
    color: #999;
    margin-left: .09375rem;
}

/* 过期图片 */
.expired {
    width: .7031rem;
    height: .7031rem;
    border: 1px solid #999;
    border-radius: 50%;
    top: .14rem;
    right: .046875rem;
    position: absolute;
    text-align: center;
    line-height: .7031rem;
}
.expired p {
    border-radius: 50%;
    color: #999;
    font-size: 0.16rem;
}
</style>



