<template>
  <div class="invoiceTitle">
    <div class="intitle">
      <div class="inleft" @click="titled()">
        <i class="fa fa-angle-left fa-2x"></i>
      </div>
      <span class="inSpan">选择发票类型</span>
    </div>
    <div class="oins">
      <div class="onInv">
        <span class="onSpan">不需要开发票</span>
        <span class="onSpan1" :class="show!=''&&'onSpan2'" @click="show=!show">✔</span>
      </div>
      <div class="butDiv">
        <router-link to="/confirmOrder">
        确定
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: "invoiceTitle",
  data() {
      return {
          show:false
      }
  },
  
  methods: {

    titled() {
      this.$router.go(-1);
    }
  }
};
</script>
<style scoped>
.oins {
    width: 100%;
    height: 100%;
  background-color: rgb(246, 242, 242);
  overflow: hidden;
}
.intitle {
  width: 100%;
  height: 0.457rem;
  background: #3190e8;
  line-height: 0.457rem;
  text-align: center;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
.inleft {
  float: left;
  padding-left: 0.14rem;
}
.inSpan {
  font-size: 0.2rem;
  font-weight: bold;
  margin-left: -0.3rem;
}
.onInv {
  width: 100%;
  height: 1rem;
  margin-top: 0.457rem;
  background-color:white;
  font-size: 0.2rem;
  line-height: 1rem;
}
.onSpan{
     margin-left: 0.2rem;
}
.butDiv{
    width: 100%;
    height: 0.3rem;
    text-align: center;
    line-height: 0.3rem;
    background-color: blue;
    margin-top: 0.1rem;
}
.onSpan1{
    position: relative;
    top: 0;
    left: 1.7rem;
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    text-align: center;
    line-height: 0.2rem;
    background-color: green;
    color: white;
}
.onInv .onSpan2{
    background-color: gray;
    color: gainsboro;
}
</style>



