<template>
  <div class="balance">
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>我的余额</div>
    </header>

    <!-- 当前余额部分 -->
    <section class="con_container">
      <section class="content">
        <div class="con_header">
          <span class="header_title">当前余额</span>

          <router-link to="/balanceIssue">
            <span class="header_description">
              <i class="el-icon-question"></i>
              余额说明
            </span>
          </router-link>
        </div>

        <section class="con_num">
          <span>{{this.$store.state.userInfo.balance}}.00</span>
          <span>元</span>
        </section>

        <div class="con_btn">提现</div>
      </section>
    </section>

    <p class="deal_detail">交易明细</p>

    <!-- logo部分 -->
    <div class="balan_logo">
      <img :src="image" alt>
      <p>暂无明细记录</p>
    </div>
  </div>
</template>

<script>
import image from "../../../../assets/账单.png";

export default {
  name: "balance",
  data() {
    return {
      image
    };
  },
  methods: {
    back1() {
      this.$router.go(-1);
    },
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.46rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.46rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 当前余额部分 */
.con_container {
  padding: 0.07rem;
  background: #3190e8;
  margin-top: 0.46rem;
}

.content {
  padding: 0.09rem;
  background: white;
  border-radius: 0.04rem;
}

/* 内容title */
.con_header {
  display: flex;
  justify-content: space-between;
  font-size: 0.13rem;
  line-height: 0.17rem;
}
.header_title {
  color: #666;
}

/* 余额说明 */
.header_description {
  display: flex;
  align-items: center;
  color: #3190e8;
}
.header_description i {
  font-size: 0.14rem;
  margin-right: 0.05rem;
}

.con_num {
  font-family: Helvetica Neue, Tahoma, Arial;
  height: 0.51rem;
}
.con_num span:first-child {
  font-size: 0.42187rem;
  color: #333;
}
.con_num span:nth-of-type(2) {
  font-size: 0.164rem;
  color: #333;
}

/* 提现 */
.con_btn {
  width: 100%;
  height: 0.46875rem;
  font-size: 0.1875rem;
  color: #fff;
  border-radius: 0.04rem;
  line-height: 0.46875rem;
  margin-top: 0.234375rem;
  text-align: center;
  background-color: #ccc;
}

/* 交易明细 */
.deal_detail {
  font-size: 0.1406rem;
  color: #999;
  line-height: 0.46875rem;
  padding-left: 0.117rem;
}

/* logo部分 */
.balan_logo {
  text-align: center;
  margin-top: 0.234375rem;
}
.balan_logo img {
  width: 1.875rem;
  height: 1.171875rem;
}
.balan_logo p {
  margin-top: 0.117rem;
  font-size: 0.164rem;
  color: #666;
  font-family: Helvetica Neue, Tahoma, Arial;
}
</style>



