<template>
  <div class="vipExchange">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>兑换会员</div>
    </header>

    <p class="buy_for">
      成功兑换后将关联到当前账号：
      <span>{{this.$store.state.userInfo.username}}</span>
    </p>

    <form class="form_style">
      <input type="text" placeholder="请输入10位卡号" maxlength="10" v-model="numten">
      <input type="text" placeholder="请输入6位卡密" maxlength="6" v-model="numsix">
    </form>

    <p
      class="determine"
      :disabled="numten.length<10&&numsix<6"
      :class="numten.length>=10&&numsix.length>=6 !=''&&'colors'"
      @click="conversion()"
    >兑换</p>

    <footer class="hint">
      <h3>——温馨提示——</h3>
      <p>新兑换的会员服务，权益以「会员说明」为准。</p>
      <p>月卡：30次减免配送费。</p>
      <p>季卡：90次减免配送费</p>
      <p>年卡：360次减免配送费</p>
      <p>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</p>
    </footer>
  </div>
</template>

<script>
import warn from "../../../../public/warn";

export default {
  name: "vipExchange",
  data() {
    return {
      numten: "",
      numsix: ""
    };
  },
  created() {},
  components: {
    warn
  },
  methods: {
    back1() {
      this.$router.go(-1);
    },
    conversion() {
      this.$store.state.erroMsg = "无效的卡号";
      this.$store.state.flag = true;
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

.buy_for {
  font-size: 0.14rem;
  color: #666;
  line-height: 0.46875rem;
  padding-left: 0.164rem;
  margin-top: 0.457rem;
}
.buy_for span {
  font-weight: 700;
  color: #333;
}

.form_style {
  display: flex;
  flex-direction: column;
}

.form_style input {
  border-bottom: 1px solid #f5f5f5;
  height: 0.46875rem;
  font-size: 0.15234rem;
  color: #999;
  padding-left: 0.164rem;
  border: 0 none; /*去掉边框*/
  outline-style: none; /*去掉轮廓线*/
}

.determine {
  background-color: #ccc;
  font-size: 0.164rem;
  color: #fff;
  text-align: center;
  margin: 0 0.164rem;
  line-height: 0.421875rem;
  border-radius: 0.046875rem;
  margin-top: 0.164rem;
  font-weight: 700;
}
.colors {
  background-color: #4cd964;
}

.hint {
  margin-top: 0.234375rem;
}
.hint h3 {
  text-align: center;
  font-weight: 400;
  font-size: 0.15234rem;
  color: #aaa;
  margin-bottom: 0.14rem;
}
.hint p {
  padding-left: 0.46875rem;
  font-size: 0.117rem;
  color: #aaa;
  line-height: 0.1875rem;
}
</style>



