<template>
  <div class="confirmYa">
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>确认订单</div>
    </header>
    <warn></warn>
    <div class="loginOr">
      <router-link :to="{name:'login',params:{bj:1}}" v-if="!$store.state.userInfo.id">登陆|注册</router-link>
      <router-link :to="{name:'personal'}" v-else>
        <i class="fa fa-user-o"></i>
      </router-link>
    </div>
    <div class="confirBody">
      <div class="address">
        <div v-if="addInfo==''">
          <router-link to="/getOrder">
            <span>
              <i class="fa fa-map-marker fa-2x"></i>
            </span>
            
            <span class="toR fr">
              <i class="fa fa-angle-right fa-2x"></i>
            </span>
            <div class="shdz fr">请添加一个收货地址</div>
          </router-link>
        </div>
        <div v-else>
          <router-link to="/getOrder">
            <span class="fl">
              <i class="fa fa-map-marker fa-2x"></i>
            </span>
            <div class="shdzYa fl">
              <p>
                {{addInfo.name}}
                {{addInfo.sex}}{{addInfo.phone}}
              </p>
              <p>{{addInfo.tag_type}}{{addInfo.address+addInfo.address_detail}}</p>
            </div>
            <span class="toRYa">
              <i class="fa fa-angle-right fa-2x"></i>
            </span>
          </router-link>
        </div>
      </div>
      <div class="reachTime">
        <p class="rTime fl">送达时间</p>
        <div class="fr">
          <p class="bb">尽快送达 | 预计{{newTime}}</p>
          <span class="birdYa">蜂鸟专送</span>
        </div>
      </div>
      <div class="payKinds">
        <header>
          <span>支付方式</span>
          <div v-if="!show" class="headerDiv" @click="show=!show">
            <span>在线支付</span>
            <div class="headeri">
              <i class="fa fa-angle-right fa-2x"></i>
            </div>
          </div>
        </header>
        <div class="hotBag">
          <span>红包</span>
          <span class="spanHot">暂时只在饿了么APP中支持</span>
        </div>
      </div>
      <div class="orderLists">
        <header class="header2">
          <img :src="$store.state.shopBaseUrl + $store.state.shopInfo.image_path" alt class="bImg">
          {{this.$store.state.shopInfo.name}}
        </header>
        <ul class="foodList">
          <!-- <li v-for=''></li> -->
          <li v-for="(site,key,index) in foodNum" :key="index">
            <p v-if="site.name && key != site.name && site.num>0">
              <strong>{{key}}</strong>
              <span class="fr">￥{{site.price}}</span>
              <span class="fr bs">x{{site.num}}</span>
            </p>
            <p v-else-if="site.num>0" class="toFr">
              <strong>{{site.name}}</strong>
              <span class="fr">￥{{site.price}}</span>
              <span class="fr bs numYa">x{{site.num}}</span>
            </p>
          </li>
        </ul>
        <div class="foodList delivery">
          <span>餐盒</span>
          <div class="cardBox">
            <span></span>
            <span>￥{{cardBox}}</span>
          </div>
        </div>
        <div class="foodList delivery">
          <span>配送费</span>
          <span class="fr">￥{{this.$store.state.shopInfo.float_delivery_fee}}</span>
        </div>
        <div class="ordList">
          <p class="pList">订单 ￥{{allMoney}}</p>
          <div class="waitToPay">
            <p class="bs">待支付</p>
            <p class="Mon bs">￥{{allMoney}}</p>
          </div>
        </div>
      </div>
      <div class="payWay">
        <div class="pay1" @click="goBz()">
          <span>订单备注</span>
          
          <span
            class="Ko"
            v-if="!textSb1">口味偏好等</span>
          <span class="Ko" v-else>
            {{textSb1}}
          </span>
        </div>

        <div class="pay1" @click="goKf()">
          <span>发票抬头</span>
          
          <span class="Ko Ko1">不需要开发票</span>
        </div>
      </div>
      <div class="order">
        <span>待支付￥{{allMoney}}</span>
        <div class="cOrder" @click="judge()">确认下单</div>
      </div>
    </div>
    <div v-if="show" class="titles" @click="show=!show">
      <div class="kindsDiv">
        <div class="titlePay">支付方式</div>
        <p class="titleP1">
          <span class="spanP1">货到付款(商家不支持货到付款)</span>
          <span class="span1">✔</span>
        </p>
        <p class="titleP2">
          <span class="spanP2">在线支付</span>
          <span class="span2">✔</span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import headZj from "../../../../public/header/headZj";
import warn from "../../../../public/warn";
export default {
  name: "confirmOrder",
  data() {
    return {
      foodNum: "",
      cardBox: 4000,
      allMoney: "",
      addInfo: "",
      show: false,
      textSb1: ""
    };
  },
  computed: {
    newTime: function() {
      let hours = parseInt(new Date().getHours());
      let mins = parseInt(new Date().getMinutes());

      // let str = this.$store.state.shopInfo.order_lead_time;
      // let arr = str.split("小时");
      // console.log(arr);

      // let arr1 = arr[1].split("分钟");
      // hours += parseInt(arr);

      // // console.log("分钟", arr1[0]);
      // mins += parseInt(arr1[0]);
      // if (mins > 59) {
      //   hours += 1;
      //   mins -= 60;
      // }
      // if (hours >= 24) {
      //   hours -= 24;
      // }
      // if (mins < 10) {
      //   return hours + "点" + "0" + mins + "分";
      // } else {
      //   return hours + "点" + mins + "分";
      // }
      return hours + "点" + mins + "分";
    }
  },
  components: {
    headZj,
    warn
  },
  created() {
     this.$store.state.go = '';
    this.$store.state.erroMsg = "";
    this.$store.state.flag = false;
    this.$store.state.wanted = "确认订单";
    this.foodNum = this.$store.state.foodNum;
    this.addInfo = "";
    this.allMoney =
      this.$store.state.sum +
      this.cardBox +
      this.$store.state.shopInfo.float_delivery_fee -
      0;
    if (this.$route.params.addInfo) {
      this.addInfo = this.$route.params.addInfo;
    }
    console.log(this.$route.params.radios1);
    this.textSb1 = this.$route.params.radios1;
  },

  methods: {
    back1(){
      this.$router.push({name:"product"})
    },
    goBz() {
      this.$router.push({
        name: "chooseTaste",
        params: {}
      });
    },
    goKf() {
      this.$router.push({
        name: "invoiceTitle",
        params: {}
      });
    },
    judge() {
          if(this.$store.state.userInfo.id){
            if (this.addInfo!='') {
              console.log(this.$store.state.foodNum);
              // this.$http({
              // method:'post',
              // url:'https://elm.cangdu.org/v1/users/'+this.$store.state.userInfo.id+'/carts/'+ cart_id +'/orders',
              // data:{
              //   address_id:"",
              //   restaurant_id:"",
              //   entities:""   

              // }
              // }).then((result) => {
                
              // }).catch((err) => {
                
              // });


             this.$router.push({name:"payment"})
             }
          }else{
              this.$store.state.erroMsg = '请先登录'
              this.$store.state.flag = true
          }
          
    }
  }
};
</script>
<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}
.loginOr {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10000;
  line-height: 0.4569rem;
}
.loginOr a {
  color: white;
  padding-right: 0.14rem;
}
.bImg {
  width: 0.2813rem;
  margin-bottom: -0.07rem;
}
.confirBody {
  color: #666;
  margin-top: 0.45rem;
  overflow: hidden;
  height: 7.5rem;
}
.confirm {
  background: #ededed;
}
.address {
  padding-top: 0.2rem;

  border-bottom: 0.01rem solid #ededed;
}
.address a {
  color: #666;
  display: inline-block;
}
.shdz {
  margin-right: 1.3rem;
}
.toR {
  float: right;
  margin-right: 0.14rem;
}
.shdzYa {
  margin: 0 0.1rem;
}
.toRYa {
  position: absolute;
  top: 0.8rem;
  right: 0.14rem;
}
.fa-map-marker {
  color: blue;
}
.address {
  height: 0.82rem;
  padding-left: 0.14rem;

  background: white;
}
.reachTime {
  height: 0.9375rem;
  margin-top: 0.09375rem;
  padding: 0 0.1604rem;
  border-left: 0.04688rem solid blue;
  background: white;
  overflow: hidden;
}
.fl {
  float: left;
  overflow: hidden;
}
.fr p {
  line-height: 0.46875rem;
}
.fr {
  float: right;
  overflow: hidden;
}
.rTime {
  padding-left: 0.07rem;
  line-height: 0.9375rem;
  font-weight: 700;
}

.payKinds {
  height: 0.95266rem;
  padding: 0 0.1644rem;
  margin-top: 0.09375rem;
  background: white;
}
.orderLists {
  height: 2.88438rem;
  margin-top: 0.09375rem;
  background: white;
}
.payWay {
  height: 0.95266rem;
  padding: 0 0.1644rem;
  margin-top: 0.09375rem;
  background-color: #fff;
}
.order {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 0.46875rem;
  background: green;
}
.birdYa {
  position: absolute;
  right: 0.2rem;
  color: white;
  width: 0.5625rem;
  line-height: 0.21rem;
  text-align: center;
  font-size: 0.1rem;
  background-color: #3190e8;
  border-radius: 0.02rem;
}
header {
  line-height: 0.46rem;
  width: 100%;
  overflow: hidden;
}
.headerDiv {
  float: right;
  color: #aaa;
  overflow: hidden;
  margin-right: 0.2rem;
}
.headeri {
  position: absolute;
  top: 2.64rem;
  right: 0.15rem;
  font-size: 0.1rem;
}
.hotBag {
  line-height: 0.46rem;
}
.spanHot {
  float: right;
  color: #aaa;
}
.header2 {
  padding-left: 0.14rem;
}
.foodList {
  padding: 0 0.14rem;
}
.foodList li {
  line-height: 0.42rem;
}
.delivery {
  line-height: 0.42rem;
}
.cardBox {
  display: inline-block;
  position: absolute;
  right: 0.14rem;
}
.ordList {
  height: 0.84rem;
  padding: 0 0.14rem;
  border-top: 0.01rem solid #ededed;
}
.pList {
  margin-top: 0.14rem;
  width: 1rem;
}
.waitToPay {
  float: right;
  margin-top: -0.2rem;
}
.waitToPay p {
  line-height: 0.4rem;
  font-size: 0.12rem;
}
.pay1 {
  line-height: 0.46rem;
  padding: 0 0.14rem;
   border-top:0.01rem solid #ededed;
}
.Ko {
  width: 1rem;
  height: 0.5rem;
  color: #aaa;
  text-overflow: clip;
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
   position: absolute;
  right: 0.14rem;
}

.order {
  line-height: 0.4688rem;
  color: white;
  background: #3c3c3c;
}
.order span {
  margin-left: 0.14rem;
  float: left;
  font-size: 0.16rem;
  font-weight: 700;
}
.cOrder {
  float: right;
  width: 1rem;
  height: 100%;
  background: #56d176;
  text-align: center;
}

.bs {
  color: #f60;
}
.numYa {
  margin-right: 0.3rem;
}
.bb {
  color: #3190e8;
}
.titles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
  z-index: 1000;
}
.kindsDiv {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 35%;
  background-color: #fff;
}
.titlePay {
  width: 100%;
  text-align: center;
  padding: 0.15rem 0;
  background-color: rgb(238, 232, 232);
}
.titleP1 {
  display: inline-block;
  width: 100%;
  margin-top: 0.2rem;
  color: rgb(206, 205, 205);
}
.titleP2 {
  display: inline-block;
  width: 100%;
  margin-top: 0.5rem;
}
.titleP1,
.titleP2 {
  margin-left: 0.2rem;
}
.span1,
.span2 {
  display: inline-block;
  width: 0.2rem;
  height: 0.2rem;
  background-color: gray;
  border-radius: 50%;
  text-align: center;
  line-height: 0.2rem;
  position: absolute;
  right: 0.5rem;
  color: white;
}
.span2 {
  background-color: green;
}
</style>



