<template>
  <div class="exchange">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>兑换红包</div>
    </header>

    <section class="exchange_code">
      <input type="text" placeholder="请输入兑换码" class="code_ipt" v-model="ipt1">
      <div class="code_container">
        <input type="text" placeholder="验证码" v-model="ipt2">
        <div class="container_img">
          <img :src="code&&code" alt>
          <div @click="getCode()" class="change_img">
            <p>看不清</p>
            <p>换一张</p>
          </div>
        </div>
      </div>
    </section>

    <button
      class="determine"
      @click="determines()"
      :disabled="ipt2.length<=3&&ipt1.length<=1"
      :class="ipt2.length>=4&&ipt1.length!= ''&&'determine_color'"
    >兑换</button>
  </div>
</template>
<script>
import warn from "../../../../public/warn";

export default {
  name: "exchange",
  data() {
    return {
      code: "",
      ipt1: "",
      ipt2: "",
      message: ""
    };
  },
  created() {
    this.exchange();
    this.getCode();
  },
  components: {
    warn
  },
  methods: {
    back1() {
      this.$router.go(-1);
    },
    getCode() {
      const url = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        method: "post",
        url: url,
        withCredentials: true
      }).then(res => {
        if (res.status == 200) {
          this.code = res.data.code;
        }
      });
    },
    exchange() {
      const url =
        "https://elm.cangdu.org/v1/users/" +
        this.$store.state.userInfo.id +
        "/hongbao/exchange";
      this.$http({
        method: "post",
        url: url
      }).then(res => {
        console.log(res.data);
        this.message = res.data.message;
        console.log(this.message);
      });
    },
    determines() {
      this.$store.state.erroMsg = this.message;
      this.$store.state.flag = true;
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

.exchange_code {
  margin-top: 0.621rem;
  padding: 0 0.117rem;
}

.code_ipt {
  /*去掉边框*/
  border: 0 none;
  /*去掉轮廓线*/
  outline-style: none;
  width: 3.3281rem;
  font-size: 0.164rem;
  color: #666;
  padding: 0.14rem 0.09375rem;
  border-radius: 0.035rem;
}
.code_container {
  height: 0.5156rem;
  display: flex;
  margin-top: 0.164rem;
}
.code_container input {
  /*去掉边框*/
  border: 0 none;
  /*去掉轮廓线*/
  outline-style: none;
  font-size: 0.164rem;
  color: #666;
  padding-top: 0.09375rem;
  padding-left: 0.09375rem;
  padding-bottom: 0.09375rem;
  border-radius: 0.035rem;
  flex: 3;
}
.container_img {
  display: flex;
  align-items: center;
  flex: 2;
  margin-left: 0.07rem;
  background-color: #fff;
  padding-left: 0.04687rem;
  border-radius: 0.035rem;
}
.container_img img {
  width: 0.82rem;
  height: 0.35156rem;
  margin-right: 0.04687rem;
}

.change_img {
  display: flex;
  flex-wrap: wrap;
  width: 0.46875rem;
  justify-content: center;
}
.change_img p {
  font-size: 0.1289rem;
  color: #666;
}
.change_img p:nth-of-type(2) {
  color: #3b95e9;
  margin-top: 0.04687rem;
}

/* 兑换 */
.determine {
  background-color: #ccc;
  font-size: 0.164rem;
  color: #fff;
  text-align: center;
  margin: 0 0.164rem;
  line-height: 0.42187rem;
  border-radius: 0.04687rem;
  margin-top: 0.164rem;
  width: 3.4219rem;
  /*去掉边框*/
  border: 0 none;
  /*去掉轮廓线*/
  outline-style: none;
}
.determine_color {
  background-color: #4cd964;
}
</style>



