<template>
  <div class="payment">
    <warn></warn>
    <!-- 头部 -->
    <header class="head">
      <div class="el-icon-arrow-left" @click="back1()"></div>
      <div>在线支付</div>
    </header>

    <!-- 剩余时间 -->
    <section class="residue_time">
      <section>
        <header class="time_last">支付剩余时间</header>
        <p class="time">00：{{minute}}：{{second}}</p>
      </section>
    </section>

    <div class="pay_way">选择支付方式</div>

    <!-- 图标部分 -->
    <section class="pay_way_list" v-for="(list,index) in lists" :key="index">
      <section class="item">
        <div class="pay_icon">
          <img :src="list.imgs" alt>
          <span>{{list.text}}</span>
        </div>
        <i class="el-icon-success" :class="{cut:changeCut == index}" @click="cut(index)"></i>
      </section>
    </section>

    <!-- 确认按钮 -->
    <p class="determine" @click="btn()">确认支付</p>
  </div>
</template>

<script>
import warn from "../../../../public/warn";

export default {
  name: "payment",
  data() {
    return {
      minutes: 15,
      seconds: 0,
      lists:[
        {
          text:"支付宝",
          imgs:require("../../../../../assets/zhifubao.png"),
          index:0
        },
        {
          text:"微信",
          imgs:require("../../../../../assets/vx.png"),
          index:1
        }
      ],
      changeCut:0
    };
  },
  created(){
   
  },
  mounted() {
    this.timer()
  },
  methods: {
    
    back1() {
      this.$router.push({name:"elmvip"});
    },
    num(n) {
      return n < 10 ? "0" + n : "" + n;
    },
    timer() {
      var _this = this;
      var time = window.setInterval(function() {
        if (_this.seconds === 0 && _this.minutes !== 0) {
          _this.seconds = 59;
          _this.minutes -= 1;
        } else if (_this.minutes === 0 && _this.seconds === 0) {
          _this.seconds = 0;
          window.clearInterval(time);
          _this.overtime()
        } else {
          _this.seconds -= 1;
        }
      }, 1000);
    },
    popus(){
        this.$store.state.erroMsg = "暂不开放支付功能";
        this.$store.state.flag = true;
    },
    overtime(){
        this.$store.state.erroMsg = "支付超时";
        this.$store.state.flag = true;
    },
    btn(){
        this.$store.state.erroMsg = "当前环境无法支付,请打开官方APP进行付款";
        this.$store.state.flag = true;
        this.$store.state.go="orderChoice"
    },
    cut(index){
      this.changeCut = index;
    }
  },
  created(){
    this.popus()
  },
  watch: {
    second: {
      handler(newVal) {
        this.num(newVal);
      }
    },
    minute: {
      handler(newVal) {
        this.num(newVal);
      }
    }
  },
  components:{
    warn
  },
  computed: {
    second() {
      return this.num(this.seconds);
    },
    minute() {
      return this.num(this.minutes);
    }
  }
};
</script>

<style scoped>
/* 头部 */
.head {
  background-color: #3190e8;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  line-height: 0.457rem;
  color: white;
  overflow: hidden;
}
.head div:first-child {
  font-size: 0.23rem;
  margin-left: 0.09rem;
  float: left;
  line-height: 0.457rem;
}
.head div:last-child {
  font-size: 0.187rem;
  color: #fff;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  text-align: center;
}

/* 剩余时间 */
.residue_time {
  background-color: #fff;
  padding: 0.164rem;
  text-align: center;
  margin-top: 0.457rem;
}

.time_last {
  font-size: 0.14rem;
  color: #666;
  margin-top: 0.234375rem;
  line-height: 0.19rem;
}

.time {
  font-size: 0.35156rem;
  color: #333;
  margin: 0.07rem 0 0.234375rem;
  line-height: 0.42rem;
}

.pay_way {
  background-color: #f1f1f1;
  padding: 0 0.164rem;
  font-size: 0.164rem;
  color: #666;
  line-height: 0.421875rem;
}

/* 图标部分 */
.pay_way_list {
  background-color: #fff;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.09375rem 0.164rem;
  line-height: 0.609375rem;
  border-bottom: 0.05859rem solid #f5f5f5;
}

.pay_icon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pay_icon img {
  width: 0.46875rem;
  height: 0.46875remrem;
  margin-right: 0.046875rem;
}
.pay_icon span {
  font-size: 0.164rem;
  color: #666;
}
.el-icon-success {
  font-size: 0.2444rem;
  color: #ccc;
}
.cut {
  color: #4cd964;
}

/* 确认按钮 */
.determine {
  background-color: #4cd964;
  font-size: 0.164rem;
  color: #fff;
  text-align: center;
  margin: 0 0.164rem;
  line-height: 0.421875rem;
  border-radius: 0.046875rem;
  margin-top: 0.117rem;
  font-weight: 700;
}
</style>



