<template>
    <div class="voucher">

        <!-- 代金券说明 -->
        <router-link to="/saidvoucher">
            <section class="voucher_title">
                <i class="el-icon-question"></i>
                <span>商家代金券说明</span>
            </section>
        </router-link>

        <!-- content部分 -->
        <section class="voucher_con">
            <img :src="img1" alt="">
            <p>无法使用代金券</p>
            <p>非客户端或客户端版本过低</p>
            <router-link to="/download">
               <div class="download_app">下载或升级客户端</div>
            </router-link>
        </section>
    </div>
</template>
<script>
import img1 from '../../../../../assets/person.png'

export default {
    name:"voucher",
    data() {
        return {
            img1
        }
    },
    created() {
        
    },
    methods: {
        back1(){
            this.$router.go(-1);
        },
    },
}
</script>

<style scoped>
/* title */
.voucher_title {
    font-size: .117rem;
    line-height: .46875rem;
    margin-left: 2.55rem;
}
.voucher_title i{
    font-size: 0.14rem;
    margin-right: .02rem;
    color: #3190e8;
}
.voucher_title span {
    color: #3190e8;
}

/* content部分 */
.voucher_con {
    text-align: center;
    margin-top: 0.9375rem;
}
.voucher_con img {
    width: 1.40625rem;
    height: 0.796875rem;
}
.voucher_con p:first-of-type {
    font-size: .164rem;
    color: #666;
    margin-top: .09375rem;
    line-height: 0.21rem;
}
.voucher_con p:nth-of-type(2) {
    font-size: .117rem;
    color: #999;
    margin-top: .07rem;
    margin-bottom: .07rem;
    line-height: 0.16rem;
}

.download_app {
    width: 1.2183rem;
    height: 0.2006rem;
    line-height: 0.2006rem;
    background-color: #56d176;
    font-size: .15234rem;
    color: #fff;
    padding: .07rem;
    border-radius: .035156rem;
    position: relative;
    left: 50%;
    transform: translateX(-0.68rem)
}
</style>



