<template>
  <div class="foodContain">
    <!-- <div class="bjzz" :class="[$store.state.bgC==true?'bgC':'']">

    </div>-->
    <!--  -->
    <!--  -->
    <div class="menuContain">
      <div class="leftBar">
        <ul class="leftUl">
          <li
            v-for="(foodList,index) in foodLists"
            :key="index"
            @click="colChange(index)"
            :class="{'active': index == active}"
          >
            <!-- <img :src="'https://fuss10.elemecdn.com/5/da'+ foodList.icon_url" alt> -->
            <span>{{foodList.name}}</span>
            <span class="bigNum" v-if="bigNum[index]>0">{{$store.state.bigNum[index]}}</span>
          </li>
        </ul>
      </div>
      <div class="rightBar">
        <ul class="rightUl">
          <div class="detailHead">
            <div class="hotTitle">
              <strong>{{clickFoodName}}</strong>
              <span>{{clickFoodDes}}</span>
            </div>
            <span class="threeD">...</span>
          </div>
          <li v-for="(fod,i) in clickFoodFoods" :key="i">
            <div class="detailCont">
              <div class="contTop">
                <div class="menuImg">
                  <img :src="'//elm.cangdu.org/img/'+fod.image_path" alt>
                </div>
                <div class="menuDes">
                  <h3>{{fod.name}}</h3>
                  <p class="des1">{{fod.description}}</p>
                  <p class="des2">月售{{fod.month_sales}}单 好评率{{fod.satisfy_rate}}%</p>
                  <p class="des3">
                    <span>123</span>
                  </p>
               
                </div>
                <div class="contBot">
                  <div class="priceYa">
                    <span class="priceSale">￥{{fod.specfoods[0].price}}</span>
                    <div class="addTwo" v-if="fod.specfoods.length>1 ">
                      <span v-if="many[specName] && many[specName]>0 && specName==fod.name">
                        <i class="fa fa-minus fa-2x"></i>
                      </span>
                      <span v-if="many[specName]>0 &&  specName==fod.name" class="quarity">{{many[specName]}}</span>
                      <span @click="changeWd(fod)" class="choiceDiff">选规格</span>

                      <div class="ggYa" v-show="flag==true">
                        <div class="xgg">
                          <p class="title">
                            {{fodd.name}}
                            <span @click="falseYa()">X</span>
                          </p>

                          <p class="ggTitle">规格</p>

                          <ul class="ggChoice">
                            <li
                              v-for="(spec,index) in fodd.specfoods"
                              :key="index"
                              @click="changePrice(index)"
                              :class="[index==code?'code':'']"
                            >
                              <p>{{spec.specs_name}}</p>
                            </li>
                          </ul>
                          <div class="botLine">
                            <p>￥{{foddYa.price}}</p>
                           <div class="addCartYa" @click="showLuck(foddYa)">加入购物车</div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="add" v-else>
                      <span
                        v-if="clickFoodNum[fod.name] && clickFoodNum[fod.name].num>0"
                        @click="incre(fod)"
                      >
                        <i class="fa fa-minus fa-2x"></i>
                      </span>
                      <span
                        v-if="clickFoodNum[fod.name] && clickFoodNum[fod.name].num>0"
                      >{{clickFoodNum[fod.name].num}}</span>
                      <i class="fa fa-plus fa-2x" @click="showNum(fod)"></i>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart">
      <div class="icon" @click="go()">
        <span class="cartIcon">
          <span class="allNum" v-if="$store.state.count>0">{{$store.state.count}}</span>
          <i class="fa fa-shopping-cart fa-2x"></i>
        </span>
      </div>
      <div class="price">
        <div class="sum">￥{{$store.state.sum}}</div>
        <div class="sumPrice">{{tips}}</div>
      </div>
      <div class="resolve" v-if="$store.state.sum>=20" @click="goConfirm()">
        <span>去结算</span>
      </div>
      <div class="resolve" v-else>
        <span>满20起送</span>
      </div>
    </div>
    <!-- 购物车 -->
    <div class="cartList" @click.prevent="xs=false" v-if="xs &&  $store.state.sum>0" >
      <div class="listTop" @click.stop>
        <span class="gwc">购物车</span>
        <ul>
          <li v-for="(site,key,index) in clickFoodNum" :key="index">
            <p v-if="site.name && key != site.name && site.num>0" class="toFr">
              <strong>{{key}}</strong>
              <span>
                <i class="fa fa-plus fa-2x" @click="showAdd(key);go()"></i>
              </span>
              <span>{{site.num}}</span>
              <span>
                <i class="fa fa-minus fa-2x" @click="increAdd(key);go()"></i>
              </span>
              <span>￥{{site.price}}</span>
            </p>

            <p v-else-if="site.num>0" class="toFr">
              <strong>{{site.name}}</strong>
              
              <span>
                <i class="fa fa-plus fa-2x" @click="showNum(site);go()"></i>
              </span>
              <span>{{site.num}}</span>
              <span>
                <i class="fa fa-minus fa-2x" @click="incre(site);go()"></i>
              </span>
              <span>￥{{site.price}}</span>
            </p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "product",
  data() {
    return {
      xs: false,
      show: true,
      code: 0,
      fodd:"",
      foddYa:"",
      flag: false,
      specPrice: 20,
      indexYa: 0,
      clickFood: "",
      foodLists: "",
      active: 0,
      clickFoodNum: {},
      many: [],
      specName: "",
      foodNum: [],
      bigNum: [],
      tips: "",
      clickFoodName: "",
      clickFoodDes: "",
      clickFoodFoods: ""
    };
  },
  created() {
    this.clickFood = this.foodLists[0];
    this.getFoodLists();
    this.$store.state.total = 0;
  },
  methods: {
    increAdd(key) {
      this.bigNum[this.indexYa] -= 1;
      this.$store.state.count--;
      this.clickFoodNum[key].num -= 1;
      if (this.clickFoodNum[key].num <= 0) {
        this.show = false;
      }
 this.$store.state.sum = 0
      console.log(this.clickFoodNum[key]);
      this.many[this.clickFoodNum[key].name] -= 1;
        for (var key in this.clickFoodNum) {
        this.$store.state.sum +=
          this.clickFoodNum[key].num * this.clickFoodNum[key].price;
      }

      this.$forceUpdate();
    },
    showAdd(key) {
      this.$store.state.count++;
      this.clickFoodNum[key].num += 1;
      console.log(this.clickFoodNum[key]);
      this.many[this.clickFoodNum[key].name] += 1;
this.$store.state.sum = 0
       for (var key in this.clickFoodNum) {
        this.$store.state.sum +=
          this.clickFoodNum[key].num * this.clickFoodNum[key].price;
      }
      this.$forceUpdate();
    },
    incre(fod) {
      this.bigNum[this.indexYa] -= 1;
      this.$store.state.count--;
      this.clickFoodNum[fod.name].num -= 1;
      if (this.clickFoodNum[fod.name].num <= 0) {
        this.show = false;
      }
      this.$store.state.sum = 0;
      for (var key in this.clickFoodNum) {
        this.$store.state.sum +=
          this.clickFoodNum[key].num * this.clickFoodNum[key].price;
      }
      this.$forceUpdate();
    },
    getFoodLists() {
      this.$http({
        method: "get",
        url:
          "https://elm.cangdu.org/shopping/v2/menu?restaurant_id=" +
          this.$store.state.shopInfo.id
      })
        .then(result => {
          this.foodLists = result.data;
          this.colChange(0);
          this.tips = this.$store.state.shopInfo.piecewise_agent_fee.tips;
          console.log(this.foodLists);
          if (this.$store.state.bigNum.length > 0) {
            this.bigNum = this.$store.state.bigNum;
          } else {
            for (var i = 0; i < this.foodLists.length; i++) {
              this.bigNum[i] = 0;
            }
          }
        })
        .catch(err => {});
    },
    colChange(index) {
      this.active = index;
      this.indexYa = index;
      this.clickFood = this.foodLists[index];
      this.clickFoodName = this.clickFood.name;
      this.clickFoodDes = this.clickFood.description;
      this.clickFoodFoods = this.clickFood.foods;
    },
    showInfo(spec) {
      this.specPrice = spec;
    },
    showNum(fod) {
      this.show = true;
      this.bigNum[this.indexYa] += 1;
      this.$store.state.count++;

      this.$store.state.sum = 0;

      if (!this.clickFoodNum[fod.name]) {
        this.clickFoodNum[fod.name] = {
          name: fod.name,
          price: fod.specfoods[0].price,
          id: fod.item_id,
          num: 1
        };
        console.log(this.clickFoodNum[fod.name]);
      } else {
        this.clickFoodNum[fod.name].num += 1;
        console.log(this.clickFoodNum[fod.name]);
      }

      for (var key in this.clickFoodNum) {
        this.$store.state.sum +=
          this.clickFoodNum[key].num * this.clickFoodNum[key].price;
      }

      this.$store.state.bigNum = this.bigNum;

      this.$forceUpdate();
    },
    showLuck(spec) {
      console.log(spec);
      this.show = true;
      this.specName = spec.name;

      console.log(spec);
      this.bigNum[this.indexYa] += 1;
      this.$store.state.count++;
      if (this.clickFoodNum[spec.name+spec.specs[0].value]) {
      
          console.log('1+++');
          this.clickFoodNum[spec.name+spec.specs[0].value].num += 1;
          
        } else {
         
          this.clickFoodNum[spec.name+spec.specs[0].value] = {
            name: spec.name,
            spec:spec.specs[0].value,
            price: spec.price,
            num: 1
          };
        }

      this.many[spec.name] = 0;
      this.$store.state.sum = 0;
      for (var key in this.clickFoodNum) {
        if (this.clickFoodNum[key].name == spec.name) {
        this.many[spec.name] += this.clickFoodNum[key].num;
        }
        this.$store.state.sum +=
          this.clickFoodNum[key].num * this.clickFoodNum[key].price;
      }
            this.$store.state.bigNum = this.bigNum;
      this.$forceUpdate();
      console.log(this.clickFoodNum);
    },
    changePrice(codeYa) {
      this.code = codeYa;
      this.foddYa = this.fodd.specfoods[this.code]
    },
    changeWd(fod) {
      this.code = 0;
      this.fodd = fod;
      this.foddYa = this.fodd.specfoods[this.code]
      this.flag = true;
    },
    go() {
      this.xs = true;
      this.$forceUpdate();
    },
    falseYa() {
      console.log(123);
      this.flag = false;
    },
    goConfirm() {
      this.$store.state.foodNum = this.clickFoodNum;
      this.$router.push({ name: "confirmOrder" });
    }
  }
};
</script>
