<template>
  <div class="searchPro animated fadeIn">
    <headZj></headZj>
    <div class="inputFood">
      <input type="text" placeholder="请输入商家或美食名称" class="foodInput" v-model="keywords">
      <input type="submit" value="提交" class="btn" @click="sureSearch()">
    </div>
    <div class="searchShow">
      <h4 class="searchTitle" v-show="flag==false && $store.state.foodSearchHistory!=''">搜索历史</h4>
      <h4 class="searchTitle" v-show="flag==true && searchRes!=''">商家</h4>
      <ul class="showProducter" v-if="flag && searchRes!=''">
        <li v-for="(producter,index) in searchRes" :key="index" @click="goDetail(producter)">
          <div class="proLeft">
            <img :src="baseUrl + producter.image_path" alt class="proLogo">
          </div>
          <div class="proRight">
            <p>{{producter.name}}</p>
            <p>月售{{producter.recent_order_num}}单</p>
            <p>{{producter.float_minimum_order_amount}}元起送/距离{{producter.distance}}公里</p>
            <div class="underline"></div>
          </div>
        </li>
      </ul>
      <div v-else-if="flag && searchRes == ''" class="noRes">
        <h3>很抱歉！无搜索结果！</h3>
      </div>
      <ul class="proHistory" v-else>
        <li
          v-for="(record,index) in $store.state.foodSearchHistory"
          :key="index"
          @click="insteadInput(record)"
        >{{record}}</li>
        <li class="emptyHistory" @click="emptyHistory()" v-if="$store.state. foodSearchHistory!=''">
          <span>清空搜索历史</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import headZj from '../../public/header/headZj'
export default {
  name: "searchPro",
  data() {
    return {
      flag: false,
      keywords: "",
      geohash: "",
      searchRes: "",
      baseUrl: "//elm.cangdu.org/img/"
    };
  },
  components:{
    headZj
  },
  created() {
    this.guessCity();
       this.$store.state.wanted='搜索'
  },
  methods: {
    guessCity() {
      const url = "https://elm.cangdu.org/v1/cities?type=guess";
      this.$http({
        method: "get",
        url: url
      })
        .then(result => {
          console.log(result.data.latitude);
          console.log(result.data.longitude);
          this.geohash = result.data.latitude + "," + result.data.longitude;
          console.log(this.geohash);
        })
        .catch(err => {});
    },
    goLast() {
      this.$router.go(-1);
    },
    sureSearch() {
      if (this.keywords == "") return false;

      this.$http({
        method: "get",
        url: "https://elm.cangdu.org/v4/restaurants",
        params: {
          keyword: this.keywords,
          geohash: this.geohash
        }
      })
        .then(result => {
          console.log(result);

          this.$store.state.foodSearchHistory.push(this.keywords)
          this.searchRes = result.data
          this.flag = true
          this.$store.commit("unique", [this.$store.state.foodSearchHistory,"foodSearchHistory"])
          console.log(this.$store.state.foodSearchHistory)
          // this.$set(this.$store.state.foodSearchHistory,1,123)
          console.log('aaa');
        })
        .catch(err => {});
    },
    emptyHistory() {
      console.log(123);
      this.$store.state.foodSearchHistory.splice(0);
    },
    goDetail(producter){
      console.log(123)
      this.$store.state.shopInfo=producter;
       this.$router.push({
         name:'storeDetail'
       })
    },
    insteadInput(record){
      console.log(123)
      this.keywords = record
      this.sureSearch()
    }
  },
  watch: {
    keywords: function(val) {
      if (val == "") {
        this.flag = false;
      }
    }
  }
};
</script>
<style scoped>
.animated {
  animation-duration: 1s;
}
.inputFood {
  background-color: white;
  padding: 0.11719rem 0.11719rem;
  padding-top:0.55rem;
}
.foodInput {
  width: 2.53rem;
  height: 0.34rem;
  border: 0.01rem solid #e4e4e4;
  border-radius: 0.05rem;
  background-color: #f2f2f2;
  font-weight: 700;
  padding-left: 0.15rem;
  outline: none;
}
.btn {
  float: right;
  text-align: center;
  width: 0.77rem;
  height: 0.3516rem;
  border-radius: 0.05rem;
  background-color: #3190e8;
  font-weight: 700;
  color: white;
}
.searchTitle {
  padding-left: 0.11719rem;
  line-height: 0.46rem;
  font-size: 0.14rem;
  font-weight: 700;
  color: #666;
}
.showProducter {
  background: white;
}
.showProducter li {
  height: 0.75rem;
  padding: 0.11719rem 0.11719rem;
  border-bottom: 0.012rem solid #e4e4e4;
}
.proLogo {
  width: 0.4rem;
  height: 0.4rem;
}
.proLeft {
  width: 0.4rem;
  float: left;
}
.proRight {
  width: 3.05rem;
  float: left;
  margin-left: 0.05rem;
}
p {
  line-height: 0.23rem;
  font-size: 0.13rem;
  color: #333;
}
.underline {
  margin-top: 0.05rem;
  border: 0.01rem solid #f5f5f5;
}
.proHistory li {
  padding-left: 0.14rem;
  line-height: 0.46rem;
  background: white;
  border-bottom: 0.01rem solid #e4e4e4;
}
.emptyHistory {
  text-align: center;
  background-color: #fff;
  color: #3190e8;
  font: 0.15rem Microsoft YaHei;
  font-weight: 700;
  padding-right: 0.14rem;
}
.noRes {
  background: white;
  text-align: center;
  height: 0.5rem;
  line-height: 0.5rem;
  font-weight: bold;
  color: #333;
  border-top: 0.01rem solid #e4e4e4;
  border-bottom: 0.01rem solid #e4e4e4;
}
</style>



